      "0378" : [ `In relazione ai destinatari, gli atti amministrativi si distinguono in atti particolari, atti plurimi, atti collettivi e atti generali. Gli atti rivolti a destinatari non determinati al momento dell'emanazione dell'atto, ma determinabili in un momento successivo:`, `Sono atti amministrativi generali.`, `Sono atti amministrativi collettivi.`, `Sono atti amministrativi particolari.` ] , 
      "0379" : [ `Sono atti amministrativi plurimi:`, `Gli atti formalmente unici, ma scindibili in tanti diversi provvedimenti quanti sono i destinatari.`, `Gli atti destinati ad un solo soggetto.`, `Gli atti rivolti a destinatari non determinati al momento dell'emanazione dell'atto, ma determinabili in un momento successivo.` ] , 
      "0380" : [ `In relazione alla reciproca interdipendenza, gli atti amministrativi si distinguono in:`, `Atti composti e atti contestuali.`, `Atti ampliativi e atti restrittivi.`, `Atti costitutivi e atti dichiarativi.` ] , 
      "0381" : [ `Gli atti amministrativi che risultano dalla riunione di pi atti autonomi in un'unica manifestazione esteriore sono:`, `Atti amministrativi contestuali.`, `Atti amministrativi composti.`, `Atti amministrativi plurimi.` ] , 
      "0382" : [ `In relazione alla reciproca interdipendenza, gli atti amministrativi si distinguono in composti e:`, `Contestuali.`, `Ampliativi.`, `Costitutivi.` ] , 
      "0383" : [ `In relazione alla reciproca interdipendenza, gli atti amministrativi si distinguono in atti composti e atti contestuali. Sono atti amministrativi contestuali:`, `Gli atti che risultano dalla riunione di pi atti autonomi in un'unica manifestazione esteriore.`, `Gli atti formati da pi manifestazioni di volont cos strettamente unite che si considerano un solo atto, per cui il difetto di una di esse si risolve nel difetto di tutto l'atto.`, `"Gli atti che, pur rilevando ai fini della produzione dell'effetto giuridico finale, acquistano un rilievo autonomo in seno al procedimento amministrativo o costituiscono atto finale di unprocedimento autonomo."` ] , 
