      "0386" : [ `In relazione al procedimento, gli atti amministrativi si distinguono in:`, `Atti procedimentali e atti presupposti.`, `Atti composti e atti contestuali.`, `Atti particolari, atti plurimi, atti collettivi e atti generali.` ] , 
