      "0389" : [ `"Gli atti che si inseriscono in un procedimento amministrativo e sono tra loro coordinati e preordinati all'adozione di un provvedimento amministrativo sono atti amministrativi procedimentali. Tali atti possono essere propulsivi o preparatori; tra i primi:"`, `Rientrano le istanze.`, `Rientrano i pareri.`, `Rientrano gli accordi preliminari.` ] , 
      "0390" : [ `"Gli atti che si inseriscono in un procedimento amministrativo e sono tra loro coordinati e preordinati all'adozione di un provvedimento amministrativo sono atti amministrativi procedimentali. Tali atti possono essere propulsivi o preparatori; tra i primi:"`, `Rientrano le richieste.`, `Rientrano i pareri.`, `Rientrano le designazioni.` ] , 
      "0391" : [ `"Gli atti che si inseriscono in un procedimento amministrativo e sono tra loro coordinati e preordinati all'adozione di un provvedimento amministrativo sono atti amministrativi procedimentali. Tali atti possono essere propulsivi o preparatori; tra i primi:"`, `Rientrano i ricorsi.`, `Rientrano i pareri.`, `Rientrano gli accordi preliminari.` ] , 
