      "0393" : [ `"Gli atti che si inseriscono in un procedimento amministrativo e sono tra loro coordinati e preordinati all'adozione di un provvedimento amministrativo sono atti amministrativi procedimentali. Tali atti possono essere propulsivi o preparatori; tra i secondi:"`, `Rientrano gli accordi preliminari.`, `Rientrano le richieste.`, `Rientrano i ricorsi.` ] , 
