      "0395" : [ `Sono atti amministrativi presupposti:`, `"Gli atti che, pur rilevando ai fini della produzione dell'effetto giuridico finale, acquistano un rilievo autonomo in seno al procedimento amministrativo o costituiscono atto finale di unprocedimento autonomo."`, `Gli atti che si inseriscono in un procedimento amministrativo e sono tra loro coordinati e preordinati all'adozione di un provvedimento amministrativo.`, `Gli atti che risultano dalla riunione di pi atti autonomi in un'unica manifestazione esteriore.` ] , 
      "0396" : [ `In relazione al procedimento, gli atti amministrativi si distinguono in atti procedimentali e atti presupposti. Sono atti amministrativi procedimentali:`, `Gli atti che si inseriscono in un procedimento amministrativo e sono tra loro coordinati e preordinati all'adozione di un provvedimento amministrativo.`, `"Gli atti che, pur rilevando ai fini della produzione dell'effetto giuridico finale, acquistano un rilievo autonomo in seno al procedimento amministrativo o costituiscono atto finale di unprocedimento autonomo."`, `Gli atti formati da pi manifestazioni di volont cos strettamente unite che si considerano un solo atto, per cui il difetto di una di esse si risolve nel difetto di tutto l'atto.` ] , 
      "0397" : [ `In relazione agli agenti gli atti amministrativi si distinguono in atti di un solo organo e atti di pi organi. Gli atti di un solo organo:`, `Possono essere posti in essere da un solo soggetto sia esso individuale che collettivo.`, `Possono essere posti in essere da un solo soggetto individuale.`, `Possono essere posti in essere da un solo soggetto purch collettivo.` ] , 
      "0398" : [ `"In relazione agli agenti gli atti amministrativi si distinguono in atti di un solo organo e atti di pi organi; gli atti di pi organi possono essere atti complessi, atti di concerto e contratti. Gli atti complessi:"`, `Sono anche detti codecisioni.`, `Sono anche detti generali.`, `Sono anche detti collettivi.` ] , 
      "0399" : [ `Degli atti amministrativi si operano in dottrina varie classificazioni. Indicare quale tra quelle proposte  corretta.`, `"In relazione alla natura dell'attivit esercitata, gli atti si distinguono in: atti di amministrazione attiva, atti di amministrazione consultiva ed atti di amministrazione dicontrollo."`, `In relazione al procedimento, gli atti amministrativi si distinguono in: atti composti e atti contestuali.`, `In relazione alla discrezionalit, gli atti amministrativi si distinguono in: atti di amministrazione attiva, atti di amministrazione consultiva ed atti di amministrazione di controllo.` ] , 
      "0400" : [ `Degli atti amministrativi si operano in dottrina varie classificazioni. Indicare quale tra quelle proposte  corretta.`, `In relazione alla discrezionalit, gli atti amministrativi si distinguono in: atti discrezionali e atti vincolati.`, `In relazione al procedimento, gli atti amministrativi si distinguono in: atti costitutivi e atti dichiarativi.`, `In relazione alla reciproca interdipendenza, gli atti amministrativi si distinguono in: atti costitutivi e atti dichiarativi.` ] , 
