      "0403" : [ `Degli atti amministrativi si operano in dottrina varie classificazioni. Indicare quale tra quelle proposte  corretta.`, `"In relazione ai destinatari, gli atti amministrativi si distinguono in: atti particolari; atti plurimi, atti collettivi e atti generali."`, `In relazione alla natura dell'attivit esercitata, gli atti si distinguono in: atti costitutivi e atti dichiarativi.`, `In relazione all'efficacia, gli atti amministrativi si distinguono in: atti composti e atti ampliativi.` ] , 
