      "0405" : [ `Degli atti amministrativi si operano in dottrina varie classificazioni. Indicare quale tra quelle proposte  corretta.`, `In relazione al procedimento gli atti amministrativi si distinguono in: atti procedimentali e atti presupposti.`, `In relazione alla discrezionalit, gli atti amministrativi si distinguono in: atti costitutivi e atti dichiarativi.`, `In relazione al risultato, gli atti amministrativi si distinguono in: atti plurimi e atti collettivi.` ] , 
      "0406" : [ `Degli atti amministrativi si operano in dottrina varie classificazioni. Indicare quale tra quelle proposte  corretta.`, `In relazione agli agenti gli atti amministrativi si distinguono in: atti di un solo organo e atti di pi organi.`, `In relazione al risultato, gli atti amministrativi si distinguono in: atti generali e atti particolari.`, `In relazione alla natura dell'attivit esercitata, gli atti si distinguono in: atti ampliativi e atti restrittivi.` ] , 
