      "0660" : [ `L'atto con cui il privato chiede all'autorit amministrativa di eliminare o reprimere gli abusi descritti nello stesso  denominato:`, `Denuncia.`, `Istanza.`, `Ricorso.` ] , 
      "0661" : [ `L'atto del privato con cui viene impugnato innanzi all'autorit amministrativa un provvedimento che si assume viziato per legittimit o ingiustizia nel merito  denominato:`, `Ricorso.`, `Denuncia.`, `Istanza.` ] , 
      "0662" : [ `L'atto amministrativo con cui l'autorit amministrativa competente si rivolge ad altra autorit per sollecitare l'emanazione di un atto che altrimenti non potrebbe essere emanato  denominata:`, `Richiesta.`, `Istanza.`, `Denuncia.` ] , 
      "0663" : [ `L'istanza :`, `Una domanda dei privati interessati, tendente ad ottenere un provvedimento a loro favore.`, `Un atto con cui l'autorit amministrativa competente si rivolge ad altra autorit per sollecitare l'emanazione di un atto che altrimenti non potrebbe essere emanato, non creando nell'autorit destinataria alcun obbligo di provvedere.`, `Un reclamo dell'interessato inteso a provocare un riesame di legittimit o di merito degli atti della P.A. ritenuti lesivi di diritti o interessi legittimi.` ] , 
      "0664" : [ `L'esposto :`, `Una domanda dei privati interessati, tendente ad ottenere un provvedimento a loro favore, che non genera obblighi per la P.A.`, `Una domanda dei privati interessati, tendente ad ottenere un provvedimento a loro favore, che genera per la P.A. l'obbligo di provvedere.`, `Un reclamo dell'interessato inteso a provocare un riesame di legittimit o di merito degli atti della P.A. ritenuti lesivi di diritti o interessi legittimi.` ] , 
