      "1419" : [ `I certificati rilasciati dalle P.A. attestanti stati, qualit personali e fatti non soggetti a modificazioni hanno validit illimitata. Quale certificato ha validit illimitata?`, `Estratto dell'atto di nascita.`, `Godimento dei diritti politici.`, `Iscrizione alle liste elettorali.` ] , 
      "1420" : [ `I certificati rilasciati dalle P.A. attestanti stati, qualit personali e fatti non soggetti a modificazioni hanno validit illimitata. Quale certificato ha validit illimitata?`, `Certificato di titolo di studio.`, `Stato di famiglia.`, `Certificato penale-casellario.` ] , 
      "1421" : [ `I certificati rilasciati dalle P.A. attestanti stati, qualit personali e fatti soggetti a modificazioni hanno di norma validit di sei mesi. Quale certificato ha validit limitata?`, `Godimento dei diritti politici.`, `Estratto di morte.`, `Certificato di titolo di studio.` ] , 
      "1422" : [ `I certificati rilasciati dalle P.A. attestanti stati, qualit personali e fatti soggetti a modificazioni hanno di norma validit di sei mesi. Quale certificato ha validit limitata?`, `Certificato penale.`, `Certificato di morte.`, `Certificato di nascita.` ] , 
      "1423" : [ `La pubblica amministrazione pu richiedere ad un cittadino un certificato di cittadinanza nel caso in cui all'atto della presentazione dell'istanza sia richiesta l'esibizione di un documento di identit o di riconoscimento ove tale dato risulta attestato?`, `No, il divieto  espressamente previsto dall'art. 45 del D.P.R. n. 445/2000.`, `Richiedere o meno la documentazione in tal caso  una facolt che il D.P.R. n. 445/2000 demanda all'ente interessato.`, `"La legge nulla dispone al riguardo;  da intendersi quindi che la P.A. possa agire in tal senso."` ] , 
      "1424" : [ `La pubblica amministrazione pu richiedere ad un cittadino un certificato di nascita nel caso in cui all'atto della presentazione dell'istanza sia richiesta l'esibizione di un documento di identit o di riconoscimento ove tale dato risulta attestato?`, `No, il divieto  espressamente previsto dall'art. 45 del D.P.R. n. 445/2000.`, `"Si, pu farlo legittimamente secondo quanto previsto dalD.P.R. n. 445/2000."`, `Dipende dall'amministrazione e dall'uso.` ] , 
      "1425" : [ `Il legislatore ha ulteriormente semplificato la materia delle certificazioni amministrative, estendendo i casi di utilizzo delle dichiarazioni sostitutive di certificazioni. Non possono, tuttavia, essere sostituiti da altro documento, salvo diverse disposizioni della normativa di settore,:`, `I certificati medici.`, `I certificati attestanti la qualit di pensionato.`, `I certificati attestanti l'assolvimento di specifici obblighi contributivi con l'indicazione dell'ammontare corrisposto.` ] , 
      "1426" : [ `Dispone l'art. 49 del D.P.R. n. 445/2000, che non possono essere sostituiti da altro documento, salvo diverse disposizioni della normativa di settore,:`, `I certificati di origine.`, `I certificati attestanti la qualit di studente.`, `I certificati attestanti l'assolvimento di specifici obblighi contributivi con l'indicazione dell'ammontare corrisposto.` ] , 
