      "1448" : [ ` corretto affermare che la giustizia amministrativa italiana  organizzata secondo il sistema del doppio binario?`, `Si,  organizzata secondo il sistema della doppia giurisdizione o doppio binario.`, `No,  organizzata secondo il sistema della tutela indiretta.`, `No,  organizzata secondo il sistema delle sezioni specializzate.` ] , 
