      "1641" : [ `Con riferimento ai caratteri propri del beni demaniali  corretto affermare che:`, `Non possono essere sottoposti a procedure civilistiche di espropriazione coattiva.`, `Possono appartenere a qualsiasi ente pubblico anche non territoriale.`, `Possono formare oggetto di usucapione.` ] , 
      "1642" : [ `Con riferimento ai caratteri propri del beni demaniali  corretto affermare che:`, `Non sono espropriabili.`, `Possono anche appartenere a privati.`, `Sono alienabili.` ] , 
      "1643" : [ `Con riferimento ai caratteri propri del beni demaniali  corretto affermare che:`, `Non possono formare oggetto di usucapione.`, `Possono formare oggetto di diritti a favore di terzi come qualsiasi altro bene.`, `Possono essere sottoposti a procedure civilistiche di espropriazione coattiva.` ] , 
