      "1645" : [ `Con riferimento ai beni pubblici si distinguono beni necessari, beni accidentali, beni naturali, beni artificiali. I primi:`, `Sono quei beni che per qualit intrinseche possono appartenere solo allo Stato o agli enti territoriali.`, `Sono quei beni che possono essere di propriet privata ma che, se appartengono ad un ente territoriale entrano a far parte del demanio.`, `Sono quei beni mobili che per destinazione sono asserviti a finalit pubbliche.` ] , 
      "1646" : [ `Con riferimento ai beni pubblici si distinguono beni necessari, beni accidentali, beni naturali, beni artificiali. I secondi:`, `Sono quei beni che possono essere anche di propriet privata ma che, se appartengono ad un ente territoriale entrano a far parte del demanio.`, `Sono quei beni che per qualit intrinseche possono appartenere solo allo Stato o agli enti territoriali.`, `Sono quei beni privi di naturale propensione al soddisfacimento di pubblici interesse.` ] , 
      "1647" : [ `Con riferimento ai beni pubblici si distinguono beni necessari, beni accidentali, beni naturali, beni artificiali. I terzi:`, `Sono quei beni che esistono  in natura, e tali sono tutti i beni del demanio necessario, escluso quello militare.`, `Sono quei beni mobili che per destinazione sono asserviti a finalit pubbliche.`, `Sono quei beni privi di naturale propensione al soddisfacimento di pubblici interesse.` ] , 
