      "1992" : [ `Con riferimento ai compiti del Comune per servizi di competenza statale, le funzioni relative ai servizi di anagrafe:`, `Sono esercitate dal Sindaco, quale ufficiale del Governo.`, `Sono esercitate dal Sindaco, nella veste di capo dell'amministrazione comunale.`, `Sono esercitate dal Sindaco, su delega del Prefetto.` ] , 
