      "2047" : [ `A norma di quanto dispone l'art. 5 della l. n. 936/1986, pu il presidente del CNEL, alla scadenza del mandato, essere confermato?`, `S.`, `No.`, `Si, ma in tal caso il mandato non pu superare i tre anni.` ] , 
      "2048" : [ `A norma di quanto dispone l'art. 7 della l. n. 936/1986 i membri del CNEL possono essere revocati?`, `S, su richiesta dei soggetti che li hanno designati.`, `No, mai.`, `S, ma solo su richiesta del Presidente del CNEL.` ] , 
