      "2259" : [ `La norma giuridica presenta alcuni caratteri: generalit, novit, coercibilit, positivit, ecc. Ha il carattere della generalit:`, `Perch si rivolge alla generalit degli individui o ad un gruppo pi o meno ampio di essi.`, `Perch pone prescrizioni prima inesistenti o, se vigenti, le ripropone mutandone la disciplina.`, `Perch vige solo in un determinato momento storico ed  emanata da organi e soggetti a ci legittimati dall'ordinamento.` ] , 
      "2260" : [ `La norma giuridica presenta alcuni caratteri: astrattezza, esteriorit, coercibilit, positivit, ecc. Ha il carattere della astrattezza:`, `Perch disciplina casi astratti a cui si riconducono i casi concreti.`, `Perch l'oggetto  solo l'azione che il soggetto manifesta all'esterno, a nulla rilevando dal punto di vista giuridico le sole intenzioni che ispirano l'individuo.`, `Perch la sua osservanza  assicurata dalla previsione di una sanzione che l'ordinamento associa all'ipotesi di violazione.` ] , 
