      "2262" : [ `La norma giuridica presenta alcuni caratteri: generalit, novit, esteriorit, coercibilit, positivit, ecc. Ha il carattere della novit:`, `Perch pone prescrizioni prima inesistenti o, se vigenti, le ripropone mutandone la disciplina.`, `Perch si rivolge alla generalit degli individui o ad un gruppo pi o meno ampio di essi.`, `Perch la sua osservanza  assicurata dalla previsione di una sanzione che l'ordinamento associa all'ipotesi di violazione.` ] , 
      "2263" : [ `La norma giuridica presenta alcuni caratteri: generalit, astrattezza, novit, esteriorit ecc. Ha il carattere della esteriorit:`, `Perch l'oggetto  solo l'azione che il soggetto manifesta all'esterno, a nulla rilevando dal punto di vista giuridico le sole intenzioni che ispirano l'individuo.`, `Perch si rivolge alla generalit degli individui o ad un gruppo pi o meno ampio di essi.`, `Perch pone prescrizioni prima inesistenti o, se vigenti, le ripropone mutandone la disciplina.` ] , 
      "2264" : [ `La norma giuridica presenta alcuni caratteri: generalit, astrattezza, intersoggettivit, coercibilit, ecc. Ha il carattere della coercibilit:`, `Perch la sua osservanza  assicurata dalla previsione di una sanzione che l'ordinamento associa all'ipotesi di violazione.`, `Perch disciplina casi astratti a cui si riconducono i casi concreti.`, `Perch disciplina comportamenti relativi alla condotta di un individuo nei confronti di un terzo.` ] , 
      "2265" : [ `La norma giuridica presenta alcuni caratteri: generalit, novit, esteriorit, coercibilit, ecc. Ha il carattere della positivit (effettiva vigenza):`, `Perch vige solo in un determinato momento storico ed  emanata da organi e soggetti a ci legittimati dall'ordinamento.`, `Perch disciplina casi astratti a cui si riconducono i casi concreti.`, `Perch l'oggetto  solo l'azione che il soggetto manifesta all'esterno, a nulla rilevando dal punto di vista giuridico le sole intenzioni che ispirano l'individuo.` ] , 
      "2266" : [ `Per interpretazione delle fonti legislative si intende l'attivit finalizzata a individuare le norme ricavabili da una disposizione e a determinare il loro significato linguistico. L'interpretazione estensiva:`, `Estende il significato della disposizione oltre il dato letterale.`, `Riduce l'ambito applicativo di una norma.`, `Afferma che la lettera della legge corrisponde alla volont del legislatore.` ] , 
      "2267" : [ `Per interpretazione delle fonti legislative si intende l'attivit finalizzata a individuare le norme ricavabili da una disposizione e a determinare il loro significato linguistico. L'interpretazione restrittiva:`, `Riduce l'ambito applicativo di una norma.`, `Estende il significato della disposizione oltre il dato letterale.`, `Afferma che la lettera della legge corrisponde alla volont del legislatore.` ] , 
      "2268" : [ `Per interpretazione delle fonti legislative si intende l'attivit finalizzata a individuare le norme ricavabili da una disposizione e a determinare il loro significato linguistico. L'interpretazione adeguatrice:`, `"Adatta il significato di una disposizione affinch non contrasti con il significato di altre norme di rango superiore (ad esempio una legge viene interpretata in modo che noncontrasti con la Costituzione)."`, `Estende il significato della disposizione oltre il dato letterale.`, `Riduce l'ambito applicativo di una norma.` ] , 
      "2269" : [ `Con riferimento ai criteri con cui l'attivit di interpretazione delle fonti legislative, quando l'interpretazione estende il significato della disposizione oltre il dato letterale, si parla di:`, `Interpretazione estensiva.`, `Interpretazione restrittiva.`, `Interpretazione sistematica.` ] , 
      "2270" : [ `Con riferimento ai criteri con cui l'attivit di interpretazione delle fonti legislative, quando l'interpretazione riduce l'ambito applicativo di una norma, si parla di:`, `Interpretazione restrittiva.`, `Interpretazione estensiva.`, `Interpretazione sistematica.` ] , 
      "2271" : [ `Con riferimento ai criteri con cui l'attivit di interpretazione delle fonti legislative, quando adatta il significato di una disposizione affinch non contrasti con il significato di altre norme di rango superiore (ad esempio una legge viene interpretata in modo che non contrasti con la Costituzione), si parla di:`, `Interpretazione adeguatrice.`, `Interpretazione estensiva.`, `Interpretazione restrittiva.` ] , 
      "2272" : [ `Con riferimento ai criteri con cui l'attivit interpretativa delle leggi  svolta  possibile distinguere fra una interpretazione letterale, sistemica, adeguatrice, ecc. L'interpretazione  letterale:`, `Quando si attribuisce alle disposizioni il significato proprio delle parole cos come risulta dall'uso comune e dalle connessioni sintattiche tra le stesse.`, `Quando si inserisce la disposizione da interpretare in connessione con le altre disposizioni e in relazione ai principi fondamentali dell'ordinamento.`, `Quando la disposizione da interpretare viene adattata al contesto storico, sociale e culturale in cui deve essere di volta in volta applicata.` ] , 
      "2273" : [ `Con riferimento ai criteri con cui l'attivit interpretativa delle leggi  svolta  possibile distinguere fra una interpretazione letterale, sistemica, adeguatrice, ecc. L'interpretazione  sistematica:`, `Quando si inserisce la disposizione da interpretare in connessione con le altre disposizioni e in relazione ai principi fondamentali dell'ordinamento.`, `Quando si attribuisce alle disposizioni il significato proprio delle parole cos come risulta dall'uso comune e dalle connessioni sintattiche tra le stesse.`, `Quando la disposizione da interpretare viene letta alla luce della volont del legislatore che l'ha formulata, cos come previsto dall'art. 12, comma 1, delle disposizioni sulla legge in generale.` ] , 
      "2274" : [ `Con riferimento ai criteri con cui l'attivit interpretativa delle leggi  svolta  possibile distinguere fra una interpretazione letterale, sistemica, evolutiva, ecc. L'interpretazione  evolutiva:`, `Quando la disposizione da interpretare viene adattata al contesto storico, sociale e culturale in cui deve essere di volta in volta applicata.`, `Quando si attribuisce alle disposizioni il significato proprio delle parole cos come risulta dall'uso comune e dalle connessioni sintattiche tra le stesse.`, `Quando si inserisce la disposizione da interpretare in connessione con le altre disposizioni e in relazione ai principi fondamentali dell'ordinamento.` ] , 
