      "2277" : [ `Con riferimento ai criteri con cui l'attivit interpretativa delle leggi  svolta  possibile distinguere fra una interpretazione letterale, adeguatrice, sistemica, evolutiva, storica, ecc. Quando la disposizione da interpretare viene adattata al contesto storico, sociale e culturale in cui deve essere di volta in volta applicata, l'interpretazione :`, `Evolutiva.`, `Letterale.`, `Sistematica.` ] , 
