      "2279" : [ `Con riferimento ai soggetti chiamati a svolgere l'attivit interpretativa delle leggi essa pu essere giudiziale, autentica, dottrinale, burocratica, dei consociati. L'interpretazione giudiziale:`, `" compiuta dai giudici; il risultato in questo caso  una norma giuridica che serve da fondamento per una decisione che ha valore giuridico."`, ` operata dal legislatore che interviene per fissare il significato delle disposizioni normative contenute in leggi precedenti.`, ` compiuta dall'individuo al fine di decidere se adeguare o meno la propria condotta a quanto prescritto dalle norme giuridiche.` ] , 
      "2280" : [ `Con riferimento ai soggetti chiamati a svolgere l'attivit interpretativa delle leggi essa pu essere giudiziale, autentica, dottrinale, burocratica, dei consociati. L'interpretazione autentica:`, ` operata dal legislatore che interviene per fissare il significato delle disposizioni normative contenute in leggi precedenti.`, `" compiuta dai giudici; il risultato in questo caso  una norma giuridica che serve da fondamento per una decisione che ha valore giuridico."`, `" prodotta dai c.d. esperti che studiano il diritto; in questo caso l'interpretazione non costituisce la forma risolutiva di una controversia."` ] , 
      "2281" : [ `Con riferimento ai soggetti chiamati a svolgere l'attivit interpretativa delle leggi essa pu essere giudiziale, autentica, dottrinale, burocratica, dei consociati. L'interpretazione dottrinale:`, `" prodotta dai c.d. esperti che studiano il diritto; in questo caso l'interpretazione non costituisce la forma risolutiva di una controversia."`, ` compiuta ad opera del Ministro che vincola tutti gli uffici del Ministero e tutti i soggetti esterni che vengono a contatto con tali uffici.`, ` compiuta dall'individuo al fine di decidere se adeguare o meno la propria condotta a quanto prescritto dalle norme giuridiche.` ] , 
      "2282" : [ `Con riferimento ai soggetti chiamati a svolgere l'attivit interpretativa delle leggi essa pu essere giudiziale, autentica, dottrinale, burocratica, dei consociati. L'interpretazione burocratica:`, ` compiuta ad opera del Ministro che vincola tutti gli uffici del Ministero e tutti i soggetti esterni che vengono a contatto con tali uffici.`, ` operata dal legislatore che interviene per fissare il significato delle disposizioni normative contenute in leggi precedenti.`, `" prodotta dai c.d. esperti che studiano il diritto; in questo caso l'interpretazione non costituisce la forma risolutiva di una controversia."` ] , 
      "2283" : [ `Con riferimento ai soggetti chiamati a svolgere l'attivit interpretativa delle leggi essa pu essere giudiziale, autentica, dottrinale, burocratica, dei consociati. L'interpretazione dei consociati:`, ` compiuta dall'individuo al fine di decidere se adeguare o meno la propria condotta a quanto prescritto dalle norme giuridiche.`, `" prodotta dai c.d. esperti che studiano il diritto; in questo caso l'interpretazione non costituisce la forma risolutiva di una controversia."`, ` compiuta ad opera del Ministro che vincola tutti gli uffici del Ministero e tutti i soggetti esterni che vengono a contatto con tali uffici.` ] , 
