      "2313" : [ `L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: assolute, relative, di legge costituzionale, di legge formale, rinforzate e implicite. Quando escludono la possibilit di disciplinare certe materie con fonti di grado secondario, lasciando tale determinazione solo alla legge o a atti aventi forza di legge, la riserva :`, `Assoluta.`, `Relativa.`, `Rinforzata.` ] , 
      "2314" : [ `"L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: assolute, relative, di legge costituzionale, di legge formale, rinforzate e implicite. Quando prevedono che l'intervento della legge  previsto solo per definire le caratteristiche fondamentali della disciplina, lasciando spazio alle fonti secondarie di intervenire per definirla compiutamente, lariserva :"`, `Relativa.`, `Assoluta.`, `Rinforzata.` ] , 
      "2315" : [ `L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: assolute, relative, di legge costituzionale, di legge formale, rinforzate e implicite. Quando la materia  affidata a leggi costituzionali (ad es.: articoli 71, 116, 132, 137 comma 1 Cost.), si parla di:`, `Riserve di legge costituzionale.`, `Riserve di legge formale.`, `Riserve rinforzate.` ] , 
      "2316" : [ `L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: assolute, relative, di legge costituzionale, di legge formale, rinforzate e implicite. Quando si riferiscono solo alla legge formale, approvata dal Parlamento, e non anche agli atti equiparati alla legge o alla legge regionale (ad es.: articoli 77 e 78 Cost.), si parla di:`, `Riserve di legge formale.`, `Riserve assolute.`, `Riserve rinforzate.` ] , 
      "2317" : [ `L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: assolute, relative, di legge costituzionale, di legge formale, rinforzate e implicite. Quando la Costituzione, nel riservare la materia alla legge, determina anche ulteriori limiti di contenuto (ad es.: articolo 16 Cost.), si parla di:`, `Riserve rinforzate.`, `Riserve assolute.`, `Riserve di legge costituzionale.` ] , 
      "2318" : [ `L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: riserve assolute, relative, di legge costituzionale, ecc. Si parla di riserve assolute:`, `Quando escludono la possibilit di disciplinare certe materie con fonti di grado secondario, lasciando tale determinazione solo alla legge o a atti aventi forza di legge.`, `Quando l'intervento della legge  previsto solo per definire le caratteristiche fondamentali della disciplina, lasciando spazio alle fonti secondarie di intervenire per definirla compiutamente.`, `Quando si riferiscono solo alla legge formale, approvata dal Parlamento, e non anche agli atti equiparati alla legge o alla legge regionale (ad es.: articoli 77 e 78 Cost.).` ] , 
      "2319" : [ `L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: riserve assolute, relative, di legge costituzionale, ecc. Si parla di riserve relative:`, `Quando l'intervento della legge  previsto solo per definire le caratteristiche fondamentali della disciplina, lasciando spazio alle fonti secondarie di intervenire per definirla compiutamente.`, `Quando escludono la possibilit di disciplinare certe materie con fonti di grado secondario, lasciando tale determinazione solo alla legge o a atti aventi forza di legge.`, `Quando si riferiscono solo alla legge formale, approvata dal Parlamento, e non anche agli atti equiparati alla legge o alla legge regionale (ad es.: articoli 77 e 78 Cost.).` ] , 
      "2320" : [ `L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: riserve assolute, relative, di legge costituzionale, ecc. Si parla di riserve di legge costituzionale:`, `"Quando la materia  affidata a leggi costituzionali (ad es.: articoli 71, 116, 132, 137 comma 1 Cost.); in tal caso la riserva  sempre assoluta."`, `Quando l'intervento della legge  previsto solo per definire le caratteristiche fondamentali della disciplina, lasciando spazio alle fonti secondarie di intervenire per definirla compiutamente.`, `Quando si riferiscono solo alla legge formale, approvata dal Parlamento, e non anche agli atti equiparati alla legge o alla legge regionale (ad es.: articoli 77 e 78 Cost.).` ] , 
      "2321" : [ `L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: riserve assolute, relative, di legge costituzionale, ecc. Si parla di riserve di legge formale:`, `Quando si riferiscono solo alla legge formale, approvata dal Parlamento, e non anche agli atti equiparati alla legge o alla legge regionale (ad es.: articoli 77 e 78 Cost.).`, `Quando escludono la possibilit di disciplinare certe materie con fonti di grado secondario, lasciando tale determinazione solo alla legge o a atti aventi forza di legge.`, `Quando l'intervento della legge  previsto solo per definire le caratteristiche fondamentali della disciplina, lasciando spazio alle fonti secondarie di intervenire per definirla compiutamente.` ] , 
      "2322" : [ `L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: riserve assolute, relative, di legge costituzionale, ecc. Si parla di riserve rinforzate:`, `Quando la Costituzione, nel riservare la materia alla legge, determina anche ulteriori limiti di contenuto (ad es.: articolo 16 Cost.).`, `Quando prevedono che l'intervento della legge  previsto solo per definire le caratteristiche fondamentali della disciplina, lasciando spazio alle fonti secondarie di intervenire per definirla compiutamente.`, `Quando si riferiscono solo alla legge formale, approvata dal Parlamento, e non anche agli atti equiparati alla legge o alla legge regionale (ad es.: articoli 77 e 78 Cost.).` ] , 
      "2323" : [ `L'istituto della riserva di legge non  omogeneo n unitario. Le riserve, infatti, si distinguono in: riserve assolute, relative, di legge costituzionale, ecc. Si parla di riserve implicite:`, `Quando non sono espressamente previste dalla Costituzione (ad esempio l'articolo 72 Cost.).`, `Quando escludono la possibilit di disciplinare certe materie con fonti di grado secondario, lasciando tale determinazione solo alla legge o a atti aventi forza di legge.`, `Quando si riferiscono solo alla legge formale, approvata dal Parlamento, e non anche agli atti equiparati alla legge o alla legge regionale (ad es.: articoli 77 e 78 Cost.).` ] , 
      "2324" : [ `I regolamenti, intesi come fonti di livello secondario:`, `Sono atti sostanzialmente legislativi, ma formalmente amministrativi.`, `Se emanati da autorit inferiori possono contrastare con i regolamenti emanati da autorit gerarchicamente superiori.`, `Possono derogare al principio di irretroattivit della legge.` ] , 
      "2325" : [ `I regolamenti, intesi come fonti di livello secondario:`, `Non possono mai derogare o contrastare con la Costituzione, n con i principi in essa contenuti.`, `Possono regolare istituti fondamentali dell'ordinamento.`, `Possono contenere sanzioni penali.` ] , 
      "2326" : [ `I regolamenti, intesi come fonti di livello secondario:`, `Non possono regolare istituti fondamentali dell'ordinamento.`, `Se emanati da autorit inferiori possono contrastare con i regolamenti emanati da autorit gerarchicamente superiori.`, `Possono derogare al principio di irretroattivit della legge.` ] , 
