      "2557" : [ `Pu essere eletto Presidente della Repubblica ogni cittadino che, oltre al possesso degli altri requisiti:`, `Abbia il godimento dei diritti civili e politici.`, `Sia stato parlamentare per almeno due legislature.`, `Sia di sesso maschile.` ] , 
