      "2754" : [ `Quale delle indicazioni che seguono, riferite all'ufficio di Presidente della Repubblica, corrisponde correttamente a precetti costituzionali?`, `Presiede il Consiglio superiore della magistratura.`, `Invia messaggi alle camere su richiesta della Presidenza del Consiglio dei Ministri.`, `Ratifica i trattati internazionale di propria iniziativa.` ] , 
      "2755" : [ `Nell'esercizio dei suoi poteri costituzionali, il Presidente della Repubblica pu sciogliere una sola Camera?`, `Si, lo prevede espressamente l'art. 88 della Costituzione.`, `Si, pu sciogliere soltanto il Senato.`, `No, il potere di scioglimento pu essere esercitato solo con riferimento ad entrambe le Camere.` ] , 
      "2756" : [ `Quale formalit costituzionale deve assolvere il Presidente della Repubblica prima di sciogliere le Camere?`, `Deve sentire i rispettivi Presidenti.`, `Deve acquisire il parere preventivo del Presidente del Consiglio dei Ministri.`, `Deve acquisire il parere preventivo degli altri organi costituzionali.` ] , 
      "2757" : [ `Secondo la Costituzione, quale dei seguenti soggetti ha il potere di sciogliere le Camere?`, `Il Presidente della Repubblica.`, `Il Ministro della Giustizia.`, `Il Presidente del Consiglio dei Ministri.` ] , 
      "2758" : [ `Secondo quanto disposto dalla Costituzione, il Presidente della Repubblica scioglie le Camere:`, `Sentiti i loro Presidenti.`, `Sentita la Corte costituzionale.`, `Sentito il Governo.` ] , 
      "2759" : [ `A norma del dettato costituzionale pu il Presidente della Repubblica procedere direttamente allo scioglimento delle Camere?`, `No, deve prima sentire i Presidenti delle Camere.`, `No, deve prima sentire il Presidente del Consiglio.`, `S.` ] , 
      "2760" : [ `Secondo la Costituzione, il Presidente della Repubblica pu sciogliere una sola Camera?`, `Si, salvo che nelle ipotesi di cui al comma 2, art. 88 Cost.`, `No.`, `Si, ma solo nel c.d. semestre bianco.` ] , 
      "2761" : [ `Nell'esercizio dei poteri attribuiti al Presidente della Repubblica in ordine allo scioglimento delle Camere,  corretto affermare che:`, `Pu procedere anche allo scioglimento di una sola Camera.`, `Lo scioglimento deve interessare necessariamente entrambe le Camere.`, `"Lo scioglimento pu riguardare anche solo la Camera dei Deputati; ma qualora si proceda allo scioglimento del Senato della Repubblica  sciolta di diritto anche l'altra Camera."` ] , 
      "2762" : [ `In quale dei seguenti casi il Presidente della Repubblica non pu sciogliere le Camere?`, `Quando si trovi negli ultimi sei mesi del suo mandato, salvo eccezioni.`, `Quando il Governo abbia manifestato la propria contrariet.`, `Quando il Consiglio di Stato abbia espresso parere negativo.` ] , 
      "2763" : [ `Il Presidente della Repubblica pu, sentiti i loro Presidenti, sciogliere le Camere o anche una sola di esse. Tale facolt:`, `Non pu essere esercitata, di norma, negli ultimi sei mesi del suo mandato.`, `Non pu essere esercitata, di norma, negli ultimi tre mesi del suo mandato.`, `Pu essere esercitata in ogni momento del suo mandato.` ] , 
      "2764" : [ `"Secondo la Costituzione, il Presidente della Repubblica pu procedere allo scioglimento delle Camere nel c.d. semestre bianco?"`, `No, salvo che coincida in tutto o in parte con gli ultimi sei mesi della legislatura.`, `No, in nessun caso.`, `S, sempre.` ] , 
      "2765" : [ `Il potere di scioglimento anticipato delle Camere di cui all'art. 88 Cost. pu essere esercitato anche nel c.d. semestre bianco?`, `No, salvo che essi coincidano, in tutto o in parte, con gli ultimi sei mesi della legislatura.`, `Si, pu esercitarlo in ogni momento, autonomamente.`, `No, salvo che essi coincidano, in tutto o in parte, con gli ultimi nove mesi della legislatura.` ] , 
      "2766" : [ `Ai sensi dell'art. 88 della Costituzione, il Presidente della Repubblica pu esercitare in ogni momento il potere di scioglimento delle Camere?`, `No, tale facolt non pu essere esercitata, di norma, negli ultimi sei mesi del suo mandato.`, `No, tale facolt non pu essere esercitata, di norma, negli ultimi sette mesi del suo mandato.`, `Si, tale facolt pu essere sempre esercitata anche negli ultimi mesi del suo mandato, sentiti i rispettivi Presidenti.` ] , 
      "2767" : [ `Lo scioglimento anticipato delle Camere (art. 88 Cost.) deve essere giustificato da ragioni obiettive e deve tendere a ristabilire il corretto funzionamento del meccanismo costituzionale. Esso non pu essere esercitato dal Presidente della Repubblica:`, `Di norma, negli ultimi sei mesi del suo mandato.`, `Di norma, negli ultimi tre mesi del suo mandato.`, `Di norma, negli ultimi due mesi del suo mandato.` ] , 
      "2768" : [ `L'art. 89 Cost. prescrive che gli atti presidenziali siano controfirmati al fine del requisito di validit degli stessi:`, `Dai ministri proponenti.`, `Dal Parlamento.`, `Dal Presidente della Corte costituzionale.` ] , 
      "2769" : [ `Secondo la Costituzione, la responsabilit degli atti del Presidente della Repubblica :`, `Dei Ministri proponenti che li hanno controfirmati.`, `Di tutto il Governo.`, `Delle Camere.` ] , 
      "2770" : [ `La Costituzione prescrive che per la loro validit gli atti del Presidente della Repubblica:`, `Siano controfirmati dai ministri proponenti.`, `Siano approvati dal Parlamento e controfirmati dal Presidente del Consiglio dei Ministri.`, `Siano muniti del parere del Consiglio di Stato.` ] , 
      "2771" : [ `Con riferimento alla responsabilit politica degli atti presidenziali (art. 89 Cost.)  corretto affermare che gli atti che hanno un valore legislativo sono controfirmati:`, `Anche dal Presidente del Consiglio dei Ministri.`, `Solo dal Ministro proponente.`, `Dal Presidente del Senato.` ] , 
      "2772" : [ `A norma dell'art. 89 Cost., chi controfirma gli atti del Capo dello Stato che hanno valore legislativo?`, `Anche il Presidente del Consiglio dei Ministri.`, `Il Ministro dell'Interno.`, `Il Sottosegretario alla Presidenza del Consiglio dei Ministri.` ] , 
      "2773" : [ `Gli atti del Presidente della Repubblica che hanno un valore legislativo sono controfirmati:`, `Anche dal Presidente del Consiglio dei Ministri.`, `Dal Presidente della Camera dei deputati.`, `Solo dal Ministro proponente.` ] , 
      "2774" : [ `Chi controfirma gli atti del Presidente della Repubblica che hanno valore legislativo?`, `Anche il Presidente del Consiglio dei Ministri.`, `Il Ministro guardasigilli.`, `Il Sottosegretario alla Presidenza del Consiglio dei Ministri.` ] , 
