      "2776" : [ `Con riferimento alla responsabilit giuridica del Presidente della Repubblica  corretto affermare che (art. 90 Cost.):`, `Non  responsabile degli atti compiuti nell'esercizio delle sue funzioni.`, `Gode di irresponsabilit giuridica ma non di quella politica.`, ` messo in stato d'accusa dal Governo.` ] , 
      "2777" : [ `Con riferimento alla responsabilit giuridica del Presidente della Repubblica  corretto affermare che (art. 90 Cost.):`, ` responsabile in caso di alto tradimento o per attentato alla Costituzione.`, ` responsabile degli atti compiuti nell'esercizio delle sue funzioni.`, ` messo in stato d'accusa dal Governo.` ] , 
      "2778" : [ `Con riferimento alla responsabilit giuridica del Presidente della Repubblica  corretto affermare che (art. 90 Cost.):`, ` messo in stato d'accusa dal Parlamento in seduta comune.`, ` responsabile degli atti compiuti nell'esercizio delle sue funzioni.`, `Gode di irresponsabilit giuridica ma non di quella politica.` ] , 
      "2779" : [ `Dispone l'art. 90 della Costituzione che il Presidente della Repubblica non  responsabile degli atti compiuti nell'esercizio delle sue funzioni, tranne che per alto tradimento o per attentato alla Costituzione. Cosa di intende per attentato alla costituzione?`, `Ogni comportamento doloso, che offendendo la personalit interna o internazionale dello Stato, costituisca una violazione del dovere di fedelt.`, `Ogni comportamento doloso diretto a sovvertire le istituzioni costituzionali o a violare la Costituzione.`, `Ogni comportamento anche non doloso che impedisca il regolare svolgimento della Presidenza della Repubblica.` ] , 
      "2780" : [ `Dispone l'art. 90 della Costituzione che il Presidente della Repubblica non  responsabile degli atti compiuti nell'esercizio delle sue funzioni, tranne che per alto tradimento o per attentato alla Costituzione. Cosa di intende per alto tradimento?`, `Ogni comportamento doloso diretto a sovvertire le istituzioni costituzionali o a violare la Costituzione.`, `Ogni comportamento anche non doloso che impedisca il regolare svolgimento della Presidenza della Repubblica.`, `Ogni comportamento doloso, che offendendo la personalit interna o internazionale dello Stato, costituisca una violazione del dovere di fedelt.` ] , 
      "2781" : [ `Cosa stabilisce la Costituzione in materia di responsabilit del Presidente della Repubblica?`, `Che il Presidente della Repubblica non  responsabile degli atti compiuti nell'esercizio delle sue funzioni.`, `Che il Presidente della Repubblica  responsabile in solido con chi ha controfirmato i suoi atti.`, `Che il Presidente della Repubblica  sempre responsabile degli atti compiuti nell'esercizio delle sue funzioni.` ] , 
      "2782" : [ `La Costituzione prevede qualche responsabilit del Presidente della Repubblica?`, `Si. Nei soli casi di alto tradimento o di attentato alla Costituzione.`, `No. Il Presidente della Repubblica non  mai responsabile degli atti compiuti nell'esercizio delle sue funzioni.`, `Si. Nel solo caso di alto tradimento.` ] , 
      "2783" : [ ` corretto affermare che il Presidente della Repubblica non  responsabile degli atti compiuti nell'esercizio delle sue funzioni?`, `Si, per espressa previsione di cui all'art. 90 della Costituzione.`, `No, il Presidente della Repubblica  responsabile degli atti compiuti nell'esercizio delle sue funzioni.`, `No, il Presidente della Repubblica non gode n di irresponsabilit giuridica n di irresponsabilit politica.` ] , 
