      "2936" : [ `"La Costituzione sancisce i principi dell'autonomia e di un forte decentramento, riconoscendo alle regioni ampi poteri politici, legislativi, finanziari e amministrativi. Essa pone anche dei limiti ai poteri delle regioni?"`, `Si, ad esempio attribuendo allo Stato la possibilit di esercitare un potere sostitutivo nei confronti degli organi delle autonomie territoriali per la tutela dell'interesse nazionale.`, `Unico limite  il divieto di istituire dazi.`, `No.` ] , 
