      "3200" : [ `"La Corte costituzionale giudica: sulle controversie relative alla legittimit costituzionale delle leggi e degli atti, aventi forza di legge, dello Stato e delle Regioni; sui conflitti di attribuzione tra i poteri dello Stato e su quelli tra lo ----------- (1), e tra le Regioni; sulle accuse promosse contro il ----------- (2). Completare conl'opzione corretta."`, `Stato e le Regioni (1) - Presidente della Repubblica (2).`, `Regioni e Comuni (1) - Presidente della Repubblica (2).`, `Stato e le Regioni (1) - Presidente del Consiglio dei Ministri(2).` ] , 
      "3201" : [ `"La Corte costituzionale giudica: sulle controversie relative alla legittimit costituzionale ----------- (1), dello Stato e delle Regioni; sui conflitti di attribuzione tra i poteri dello Stato e su quelli tra lo Stato e le Regioni, e tra le Regioni; sulle accuse promosse contro il ----------- (2), a norma della Costituzione. Completare conl'opzione corretta."`, `Delle leggi e degli atti, aventi forza di legge (1)- Presidente della Repubblica (2).`, `Delle leggi e degli atti amministrativi (1)- Presidente della Repubblica (2).`, `Delle leggi e degli atti, aventi forza di legge (1)- Presidente della Corte dei conti.` ] , 
      "3202" : [ `"La Corte costituzionale giudica: sulle controversie relative ----------- (1) delle leggi e degli atti, aventi forza di legge, dello Stato e delle Regioni; sui conflitti di attribuzione tra i poteri dello ----------- (2); sulle accuse promosse contro il Presidente della Repubblica, a norma della Costituzione. Completare con l'opzione corretta."`, `Alla legittimit costituzionale (1) - Stato e su quelli tra lo Stato e le Regioni, e tra le Regioni (2).`, `Al merito (1) - Stato e su quelli tra lo Stato e le Regioni, e tra le Regioni (2).`, `Alla legittimit costituzionale (1) - Stato (2).` ] , 
      "3203" : [ `"La Corte costituzionale giudica: sulle controversie relative alla legittimit costituzionale delle leggi ----------- (1), dello Stato e delle Regioni; sui conflitti di attribuzione tra i poteri dello Stato e su quelli tra lo Stato e le Regioni, e ----------- (2); sulle accuse promosse contro il Presidente della Repubblica, a norma della Costituzione. Completare con l'opzione corretta."`, `E degli atti, aventi forza di legge (1)- tra le Regioni (2).`, `Regolamenti  (1)- tra le Regioni (2).`, `E degli atti, aventi forza di legge (1)- tra i Comuni (2).` ] , 
