      "3215" : [ `Un terzo dei giudici costituzionali  nominato:`, `Dal Presidente della Repubblica.`, `Dal Presidente del Consiglio dei Ministri.`, `Dal Consiglio superiore della magistratura.` ] , 
      "3216" : [ `Quali dei seguenti organi dello Stato, tra gli altri,  chiamato a nominare un terzo dei componenti della Corte costituzionale?`, `Il Presidente della Repubblica.`, `Il capo del Governo.`, `Il Governo collegialmente.` ] , 
      "3217" : [ `Prevede la Costituzione che un terzo dei giudici costituzionali  nominato dalle supreme magistrature ordinaria ed amministrative. Con la locuzione giurisdizioni superiori ordinaria ed amministrative, si intende:`, `Corte di Cassazione, Consiglio di Stato e Corte dei Conti.`, `"Corte di Cassazione, Consiglio di Stato,T.A.R. e Corte dei Conti."`, `Corte di Cassazione e Consiglio di Stato.` ] , 
