      "3219" : [ `Prevede la Costituzione che un terzo dei giudici costituzionali  nominato dalle supreme magistrature ordinaria ed amministrative. Con la locuzione giurisdizioni superiori ordinaria ed amministrative, si intende oltre al Consiglio di Stato:                   `, `Corte di Cassazione e Corte dei Conti.`, `Corte di Cassazione, T.A.R. e Corte dei Conti.`, `Corte di Cassazione e T.A.R.` ] , 
      "3220" : [ `Prevede la Costituzione che un terzo dei giudici costituzionali  nominato dalle supreme magistrature ordinaria ed amministrative. Con la locuzione giurisdizioni superiori ordinaria ed amministrative, si intende oltre alla Corte dei conti:                   `, `Corte di Cassazione e Consiglio di Stato.`, `Corte di Cassazione, Consiglio di Stato e T.A.R.`, `Corte di Cassazione e T.A.R.` ] , 
      "3221" : [ `Un terzo dei giudici costituzionali  nominato:`, `Dalle Supreme magistrature ordinaria ed amministrative.`, `Dal Presidente del Consiglio dei Ministri.`, `Dal Consiglio superiore della magistratura.` ] , 
      "3222" : [ `A norma della Costituzione, un terzo dei giudici costituzionali  nominato:`, `Dalle Supreme magistrature ordinaria ed amministrative.`, `Dal Presidente del Consiglio dei Ministri.`, `Dal Consiglio superiore della magistratura.` ] , 
      "3223" : [ `Quanti giudici della Corte costituzionale sono nominati dalle supreme magistrature ordinaria e amministrative?`, `Un terzo.`, `Dieci.`, `Tutti.` ] , 
