      "3225" : [ `A norma della Costituzione, possono gli avvocati essere nominati giudici costituzionali?`, `Si, dopo venti anni di esercizio della professione.`, `No, poich avvocatura e magistratura devono essere separate.`, `Si, dopo dieci anni di esercizio della professione.` ] , 
      "3226" : [ `I Giudici costituzionali sono scelti tra particolari categorie astrattamente idonee a rivestire la carica per le loro conoscenze giuridiche  cio, tra laltro, fra gli avvocati con servizio almeno:`, `Ventennale.`, `Trentennale.`, `Decennale.` ] , 
