      "3228" : [ `Dispone la Costituzione che i giudici della Corte Costituzionale sono scelti anche:`, `Fra i professori ordinari di universit in materie giuridiche.`, `Fra gli avvocati dopo dieci anni di esercizio.`, `Fra i dirigenti generali dello Stato dopo dieci anni di servizio.` ] , 
