      "3230" : [ `I Giudici costituzionali sono scelti tra particolari categorie astrattamente idonee a rivestire la carica per le loro conoscenze giuridiche  cio, tra laltro, fra:`, `I magistrati, anche a riposo, delle giurisdizioni superiori ordinaria ed amministrative.`, `I professori ordinari di universit in materie economiche.`, `I Senatori a vita di nomina Presidenziale.` ] , 
