      "3237" : [ `I Giudici della Corte costituzionale prima di assume le funzioni devono prestare giuramento?`, `Si, per espressa previsione dell'art. 135 della Costituzione.`, `No, per espressa previsione dell'art. 135 della Costituzione.`, `Solo il Presidente della Corte costituzionale deve prestare giuramento per espressa previsione di cui all'art. 135 della Costituzione.` ] , 
      "3238" : [ `"I Giudici della Corte costituzionale sono nominati per ----------- (1), decorrenti per ciascuno di essi dal giorno del giuramento, ----------- (2) essere nuovamente nominati. Completare con l'opzione corretta."`, `Nove anni (1)- non possono (2).`, `Tre anni (1)- possono (2).`, `Cinque anni (1)- possono (2).` ] , 
      "3239" : [ `I Giudici della Corte costituzionale prima di assumere le funzioni prestano giuramento nelle mani:`, `Del Presidente della Repubblica.`, `Del Presidente del Consiglio dei Ministri.`, `Del Presidente della Camera del Senato.` ] , 
      "3240" : [ `A norma di quanto dispone l'art. 135 della Costituzione, i giudici della Corte costituzionale devono prestare giuramento di fedelt:`, `Prima di assumere le loro funzioni.`, `Entro quindici giorni dall'assunzione delle loro funzioni.`, `Entro trenta giorni dall'assunzione delle loro funzioni.` ] , 
      "3241" : [ `I giudici della Corte costituzionale prestano giuramento:`, `Dinanzi al Presidente della Repubblica.`, `Dinanzi al Consiglio dei Ministri e all'intero Governo.`, `Dinanzi al Parlamento in seduta comune.` ] , 
      "3242" : [ `Ai sensi dell'art. 135 della Costituzione e dellart. 5 legge 11 marzo 1953, n. 87. i giudici della Corte costituzionale, prima di assumere le loro funzioni, prestano giuramento:`, `Di osservanza della Costituzione e delle leggi.`, `Di fedelt alla Repubblica e di lealt al Parlamento.`, `Di lealt ai poteri dello Stato.` ] , 
      "3243" : [ `I giudici della Corte costituzionale:`, `Prima di assumere le loro funzioni, prestano giuramento di osservanza della Costituzione e delle leggi dinanzi al Capo dello Stato.`, `Possono revocare i Presidenti delle Camere.`, `Danno validit, con la propria controfirma, a tutti gli atti del Governo.` ] , 
      "3244" : [ `In quale momento i Giudici della Corte costituzionale entrano nell'esercizio delle loro funzioni?`, `Quando prestano giuramento.`, `Nel giorno successivo alle elezioni.`, `Dopo cinque giorni dalla data in cui prestano giuramento.` ] , 
      "3245" : [ `Alla scadenza del mandato, i giudici costituzionali possono essere nuovamente nominati?`, `No, non possono essere nuovamente nominati.`, `Si, ma per non pi di tre anni.`, `Si, possono essere nuovamente nominati.` ] , 
      "3246" : [ `Un giudice costituzionale nominato dalle supreme magistrature ordinaria ed amministrative pu essere nuovamente nominato alla scadenza del novennato?`, `No. La Costituzione esclude la nomina per un secondo novennato.`, `Si. La Costituzione prevede la nomina per un secondo novennato.`, `La Costituzione esclude la nomina per un secondo novennato solo per i giudici nominati dal Presidente della Repubblica.` ] , 
      "3247" : [ `Un giudice costituzionale nominato dal Presidente della Repubblica pu essere nuovamente nominato alla scadenza del novennato?`, `No, per espressa previsione costituzionale.`, `Si. La Costituzione prevede la nomina per un secondo novennato.`, `Si. La Costituzione lo prevede al fine di garantire la continuit funzionale.` ] , 
      "3248" : [ `Un giudice costituzionale nominato dal Parlamento in seduta comune pu essere nuovamente nominato alla scadenza del novennato?`, `No, non pu essere nuovamente nominato.`, `Si, pu essere nuovamente nominato.`, `Solo i giudici nominati dal Presidente della Repubblica possono essere nominati per un secondo novennato.` ] , 
      "3249" : [ `Quale  la durata in carica dei giudici della Corte costituzionale e quale  quella del Presidente della Corte costituzionale?`, `I giudici durano in carica nove anni il Presidente tre anni.`, `Nove anni sia per i giudici sia per il Presidente.`, `Tre anni sia per i giudici sia per il Presidente.` ] , 
      "3250" : [ `Alla scadenza del mandato un giudice costituzionale nominato dal Presidente della Repubblica pu essere nuovamente nominato?`, `No.`, `Si, ma da un organo diverso.`, `Si, per un'altra volta soltanto.` ] , 
      "3251" : [ `Per quanto tempo rimane in carica il Presidente della Corte costituzionale?`, `Tre anni.`, `Nove anni.`, `Cinque anni.` ] , 
      "3252" : [ `Il Presidente della Corte Costituzionale  eletto:`, `Per un triennio ed  rieleggibile.`, `Per un triennio e non  rieleggibile.`, `Per nove anni ed  rieleggibile.` ] , 
      "3253" : [ `Chi elegge il Presidente della Corte Costituzionale?`, `La stessa Corte tra i suoi membri.`, `La stessa Corte tra soggetti estranei alla Corte stessa.`, `Il Presidente della Repubblica.` ] , 
      "3254" : [ `Il Presidente della Corte costituzionale:`, ` eletto dalla Corte tra i suoi componenti.`, ` eletto dalla Corte tra i componenti di nomina del Parlamento.`, ` designato dal Parlamento.` ] , 
      "3255" : [ `Alla scadenza del mandato il Presidente della Corte costituzionale  rieleggibile?`, `Si, fermi restando i termini di scadenza dall'ufficio di giudice.`, `Si, ma solo per un altro mandato.`, `No, lo esclude espressamente l'art. 135 della Costituzione.` ] , 
      "3256" : [ `"----------- (1) elegge tra i suoi componenti, secondo le norme stabilite dalla legge, il Presidente, che rimane in carica per ----------- (2), ed  rieleggibile, fermi in ogni caso i termini di scadenza dall'ufficio di giudice. Completare con l'opzione corretta."`, `La Corte costituzionale - un triennio.`, `La Corte costituzionale - un novennato.`, `Il Presidente della Repubblica - un novennato.` ] , 
      "3257" : [ `"----------- (1) elegge tra i suoi componenti, secondo le norme stabilite dalla legge, il Presidente, che rimane in carica per un triennio, ----------- (2), fermi in ogni caso i termini di scadenza dall'ufficio di giudice. Completare con l'opzione corretta."`, `La Corte costituzionale - ed  rieleggibile.`, `La Corte costituzionale - e non  rieleggibile.`, `Il Parlamento - e non  rieleggibile.` ] , 
      "3258" : [ `"La Corte costituzionale elegge tra ----------- (1), secondo le norme stabilite dalla legge, il Presidente, che rimane in carica per ----------- (2), ed  rieleggibile, fermi in ogni caso i termini di scadenza dall'ufficio di giudice. Completare con l'opzione corretta."`, `I suoi componenti - un triennio.`, `I suoi componenti nominati dal Presidente della Repubblica - un triennio.`, `I suoi componenti - un biennio.` ] , 
      "3259" : [ `Ai sensi dell'art. 135 della Costituzione, l'ufficio di giudice della Corte costituzionale :`, `Incompatibile, tra l'altro, con la carica di consigliere regionale.`, `Compatibile solo con la carica di membro del Parlamento.`, `Incompatibile con qualsiasi altra attivit.` ] , 
      "3260" : [ `Ai sensi dell'art. 135 della Costituzione, l'ufficio di giudice della Corte costituzionale :`, `Incompatibile, tra l'altro, con l'esercizio della professione di avvocato.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile con qualsiasi altra attivit.` ] , 
      "3261" : [ `Ai sensi dell'art. 135 della Costituzione, l'ufficio di giudice della Corte costituzionale :`, `Incompatibile, tra l'altro, con la carica di membro del Parlamento.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile con qualsiasi altra attivit.` ] , 
