      "3263" : [ `L'importanza e la delicatezza delle funzioni che i giudici della Corte costituzionale svolgono comportano incompatibilit e prerogative. L'ufficio di giudice della Corte costituzionale :`, `Incompatibile anche con l'esercizio di qualsiasi ufficio o impiego pubblico o privato.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile anche con la semplice iscrizione ad un partito politico.` ] , 
      "3264" : [ `L'importanza e la delicatezza delle funzioni che i giudici della Corte costituzionale svolgono comportano incompatibilit e prerogative. L'ufficio di giudice della Corte costituzionale :`, `Incompatibile anche con l'esercizio di amministratore o sindaco di societ commerciale con fine di lucro.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile anche con la semplice iscrizione ad un partito politico.` ] , 
      "3265" : [ `L'importanza e la delicatezza delle funzioni che i giudici della Corte costituzionale svolgono comportano incompatibilit e prerogative. L'ufficio di giudice della Corte costituzionale :`, `Incompatibile anche con la presenza attiva in un partito politico e lo svolgimento di qualsiasi attivit per conto di esso.`, `Compatibile solo con la carica di membro del Parlamento.`, `Incompatibile con qualsiasi altra attivit.` ] , 
