      "3267" : [ `A norma della Costituzione, l'ufficio di giudice della Corte costituzionale :`, `Incompatibile, tra l'altro, con la candidatura in elezioni amministrative.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile con qualsiasi altra attivit.` ] , 
      "3268" : [ `Ai sensi dell'art. 135 della Costituzione, l'ufficio di giudice della Corte costituzionale :`, `Incompatibile, tra l'altro, con la candidatura in elezioni politiche.`, `Compatibile solo con la carica di consigliere regionale.`, `Incompatibile con qualsiasi altra attivit.` ] , 
      "3269" : [ `I giudici della Corte Costituzionale possono essere candidati in elezioni amministrative o politiche?`, `No, lo vieta espressamente la legge.`, `No, salvo espressa autorizzazione del Presidente della Corte Costituzionale.`, `Si, ma in caso di elezione devono dimettersi dall'ufficio di giudice costituzionale.` ] , 
