      "3271" : [ `Un consigliere regionale pu essere membro della Corte costituzionale?`, `No, l'ufficio di giudice della Corte  incompatibile anche con quello di consigliere regionale.`, `No, salva autorizzazione del Presidente della Corte stessa.`, `Si, tra i due uffici non esiste alcuna incompatibilit.` ] , 
      "3272" : [ `Dispone la Costituzione che l'ufficio di giudice della Corte costituzionale :`, `Incompatibile, tra l'altro, con la carica di consigliere regionale.`, `Compatibile solo con la carica di membro del Parlamento.`, `Incompatibile con qualsiasi altra attivit.` ] , 
      "3273" : [ `Ai sensi del Titolo VI - Garanzie Costituzionali - sulle accuse promosse contro il Presidente della Repubblica giudica:`, `La Corte costituzionale integrata dai 16 giudici aggregati o non togati.`, `Il Consiglio di Stato.`, `La Corte dei Conti.` ] , 
      "3274" : [ `Chi giudica sulle accuse promosse contro il Presidente della Repubblica a norma delle garanzie costituzionali?`, `La Corte costituzionale integrata dai 16 giudici aggregati o non togati.`, `Il Parlamento in seduta congiunta.`, `La Corte di Cassazione.` ] , 
