      "3322" : [ `"Dei compiti spettanti alla Corte Costituzionale  possibile distinguere tra: funzioni giurisdizionali e funzioni amministrative; funzioni di garanzia e funzioni arbitrali; attribuzioni di legittimit e attribuzioni di merito. Quando svolge funzioni giurisdizionali:"`, `Alla Corte spetta il compito di risolvere una vera e propria controversia.`, `La Corte opera una mera verifica sull'esistenza o meno di determinati presupposti.`, `La Corte opera come garante dei cittadini contro eventuali violazioni al testo della Costituzione.` ] , 
