      "3748" : [ `Il Regolamento della Camera dei Deputati previsto all'art. 64 della Costituzione:`, ` adottato dalla stessa Camera.`, ` adottato a maggioranza dei due terzi dei suoi componenti.`, `Disciplina l'organizzazione interna anche delle Senato.` ] , 
      "3749" : [ `Il Regolamento della Camera dei Deputati previsto all'art. 64 della Costituzione:`, ` adottato a maggioranza assoluta dei suoi componenti.`, ` adottato dalla Presidenza della Repubblica.`, `Detta le regole per il funzionamento anche per il Senato.` ] , 
