      "3911" : [ `"Il Titolo III, Sezione I della Costituzione  dedicato al Consiglio dei Ministri; indicare quale affermazione in merito  corretta."`, `Il voto contrario di una o di entrambe le Camere su una proposta del Governo non importa obbligo di dimissioni.`, `Il Presidente della Corte costituzionale nomina il Presidente del Consiglio dei Ministri.`, `Il Presidente del Senato della Repubblica dirige la politica generale del Governo e ne  responsabile.` ] , 
