      "3926" : [ `La giustizia  amministrata in nome ----------- (1). I giudici sono soggetti soltanto ----------- (2). Completare con l'opzione corretta.`, `Del popolo (1) - alla legge (2).`, `Della Nazione (1) - alla legge (2).`, `Della Repubblica (1)  al Consiglio superiore della magistratura (2).` ] , 
