      "3930" : [ `A norma del disposto di cui all'art. 102 della Costituzione, la funzione giurisdizionale pu essere esercitata da magistrati straordinari?`, `No. La funzione giurisdizionale  esercitata da magistrati ordinari istituiti e regolati dalle norme sull'ordinamento giudiziario.`, `No. La funzione giurisdizionale  esercitata solo da magistrati speciali.`, `Si. La funzione giurisdizionale  esercitata solo da magistrati straordinari.` ] , 
