      "3935" : [ `Secondo quanto dispone l'art. 102 della Costituzione, possono istituirsi, per determinate materie, sezioni specializzate presso gli organi giudiziari ordinari?`, `S.`, `No, possono solo istituirsi giudici straordinari.`, `No, possono solo istituirsi giudici speciali.` ] , 
