      "3942" : [ `In materia di giurisdizione l'art. 102 della Costituzione stabilisce che:`, `La legge regola i casi e le forme della partecipazione diretta del popolo all'amministrazione della giustizia.`, `In nessun caso cittadini estranei alla magistratura possono partecipare ad organi giudiziari.`, `La legge esclude ogni forma di partecipazione diretta del popolo all'amministrazione della giustizia.` ] , 
