      "4096" : [ ` corretto affermare che le sentenze sulla libert personale devono essere motivate?`, `Si, dispone l'art. 111 della Costituzione che tutti i provvedimenti giurisdizionali devono essere motivati.`, `Si, solo le sentenze devono essere motivate, a norma di quanto prevede l'art. 111 della Costituzione.`, `No, nessun provvedimento giurisdizionale deve essere motivato.` ] , 
