      "4100" : [ `A norma di quanto dispone l'art. 111 della Costituzione, contro le sentenze sulla libert personale pronunciate dagli organi giurisdizionali ordinari o speciali  sempre ammesso ricorso in Cassazione?`, `Si, per violazione di legge.`, `No, non  ammesso ricorso in Cassazione.`, ` ammesso ricorso in Cassazione solo per le sentenze pronunciate dagli organi giurisdizionali speciali.` ] , 
