      "4260" : [ `"La proposta soggetta a referendum  approvata se ha partecipato alla votazione la ----------- (1) degli aventi diritto, e se  raggiunta la ----------- (2) dei voti validamente espressi. Completare con l'opzione corretta."`, `Maggioranza (1) - maggioranza (2).`, `Maggioranza dei due terzi (1) - maggioranza (2).`, `Maggioranza (1) - maggioranza dei due terzi(2).` ] , 
