      "4265" : [ `Indicare quale affermazione sul referendum abrogativo  consona al disposto di cui all'art. 75 della Costituzione.`, `La proposta soggetta a referendum  approvata se ha partecipato alla votazione la maggioranza degli aventi diritto, e se  raggiunta la maggioranza dei voti validamente espressi.`, `Hanno diritto a partecipare al referendum tutti i cittadini chiamati ad eleggere il Senato della Repubblica.`, `La richiesta di referendum deve essere avanzata da cinquecentomila elettori o dieci Consigli regionali.` ] , 
      "4266" : [ `Indicare quale affermazione sul referendum abrogativo  consona al disposto di cui all'art. 75 della Costituzione.`, `La legge determina le modalit di attuazione del referendum.`, `La proposta soggetta a referendum  approvata se ha partecipato alla votazione la maggioranza degli aventi diritto, e se  raggiunta la maggioranza dei due terzi dei voti validamente espressi.`, `Hanno diritto a partecipare al referendum tutti i cittadini chiamati ad eleggere il Senato della Repubblica.` ] , 
