      "4275" : [ ` possibile depositare richiesta di referendum popolare nell'anno anteriore alla scadenza di una delle due Camere?`, `No, per espressa previsione legislativa.`, `Si, il deposito della richiesta di referendum nulla consta con l'elezione delle due Camere.`, `Non  possibile depositare richiesta di referendum popolare nei sei mesi anteriori alla scadenza di una delle due Camere.` ] , 
      "4276" : [ ` possibile depositare richiesta di referendum popolare nei sei mesi successivi alla data di convocazione dei comizi elettorali per l'elezione delle due Camere?`, `No, per espressa previsione legislativa.`, `Si, il deposito della richiesta di referendum nulla consta con l'elezione delle due Camere.`, `Si, non  possibile depositare richiesta di referendum popolare nei tre mesi successivi alla data di convocazione dei comizi elettorali.` ] , 
