      "4279" : [ ` ammissibile una richiesta di referendum abrogativo di una legge che disciplini l'elezione di organi di rilevanza costituzionale?`, `No,  inammissibile.`, `Si,  ammissibile.`, `Si, il referendum non  ammissibile solo per le leggi tributarie, di bilancio, di amnistia e indulto.` ] , 
