      "4281" : [ `La Corte Costituzionale:`, `Decide l'ammissibilit del referendum popolare abrogativo.`, `Decide la conformit alle norme di legge del referendum popolare abrogativo.`, `Indice il referendum popolare abrogativo.` ] , 
      "4282" : [ `Qualora il referendum di cui all'art. 75 della Costituzione abbia esito positivo il Presidente della Repubblica dichiara l'avvenuta abrogazione della legge, o dell'atto avente forza di legge, o delle disposizioni oggetto del referendum che ha effetto a decorrere dal giorno successivo a quello della pubblicazione del decreto nella G.U. Il Presidente della Repubblica pu ritardare l'entrata in vigore della abrogazione?`, `Si, per un termine non superiore a 60 giorni dalla data della pubblicazione.`, `Si, per un termine non superiore a 30 giorni dalla data della pubblicazione.`, `No, il ritardo dell'entrata in vigore costituisce grave violazione del dettato costituzionale.` ] , 
      "4283" : [ `Nel caso che il risultato del referendum di cui all'art. 75 della Costituzione sia contrario all'abrogazione di una legge, o di un atto avente forza di legge, o di singole disposizioni di essi:`, `Non pu proporsi referendum per l'abrogazione del quesito prima che siano trascorsi cinque anni.`, `Non pu proporsi referendum per l'abrogazione del quesito prima che siano trascorsi due anni.`, `Non si pu pi proporre referendum per lo stesso quesito.` ] , 
