      "4468" : [ `A norma di quanto dispone l'art. 78 della Costituzione chi conferisce al Governo tutti i poteri necessari per far fronte alle esigenze belliche?`, `Camere.`, `Presidenza del Consiglio dei Ministri.`, `Il Presidente della Repubblica.` ] , 
      "4469" : [ `Le Camere deliberano lo stato di guerra e conferiscono i poteri necessari:`, `Al Governo.`, `Al Presidente della Repubblica.`, `Al Presidente della Camera dei deputati.` ] , 
