      "4495" : [ `L'amnistia e l'indulto:`, `Sono deliberati dal Parlamento.`, `Sono concessi dal Ministro della Giustizia.`, `Sono concessi dal Presidente del Consiglio dei Ministri.` ] , 
      "4496" : [ `L'amnistia e l'indulto:`, `Sono concessi con legge che stabilisce il termine per la loro applicazione.`, `Possono applicarsi ai reati commessi successivamente alla presentazione del disegno di legge, ma non dopo la sua approvazione.`, `Sono concessi dal Parlamento previo parere favorevole del Ministro della Giustizia.` ] , 
      "4497" : [ `A quale organo compete, ai sensi dell'art. 80 della Costituzione, l'autorizzazione alla ratifica di trattati internazionali che importano variazioni del territorio?`, `Camere, con legge.`, `Ministro degli Affari Esteri.`, `Consiglio dei Ministri.` ] , 
      "4498" : [ `La ratifica dei trattati internazionali che importano variazioni del territorio:`, ` autorizzata dalle Camere.`, ` autorizzata dal Ministro dell'economia e delle finanze.`, ` autorizzata dal Presidente del Consiglio dei Ministri.` ] , 
      "4499" : [ `A norma del dettato costituzionale, a quale organo compete l'autorizzazione alla ratifica dei trattati internazionali che importano variazioni del territorio?`, `Alle Camere, con legge.`, `Al Presidente del Consiglio dei Ministri.`, `Al Presidente dalla Repubblica, con D.P.R.` ] , 
      "4500" : [ ` corretto affermare che le Camere, autorizzano con legge, la ratifica di trattati internazionali che importano variazioni del territorio?`, `Si, tale autorizzazione rientra nelle competenze delle Camere previste dall'art. 80 della Costituzione.`, `No, le autorizzazioni previste dall'art. 80 della Costituzione riguardano i soli trattati internazionali che importano modificazioni di leggi.`, `No, le autorizzazioni previste dall'art. 80 della Costituzione riguardano i soli trattati internazionali che importano oneri alle finanze.` ] , 
