      "4505" : [ `A quale organo compete, ai sensi dell'art. 80 della Costituzione, l'autorizzazione alla ratifica di trattati internazionali che importano modificazioni di leggi?`, `Camere, con legge.`, `Presidente del Consiglio e Ministro degli Affari Esteri, congiuntamente.`, `Consiglio dei Ministri.` ] , 
      "4506" : [ `La ratifica dei trattati internazionali che importano modificazioni di leggi:`, ` autorizzata dalle Camere.`, ` competenza propria del Presidente del Consiglio dei Ministri.`, ` autorizzata dal Ministro degli Affari Esteri.` ] , 
      "4507" : [ `La ratifica dei trattati internazionali che importano oneri alle finanze:`, ` autorizzata dalle Camere.`, ` competenza propria del Presidente del Consiglio dei Ministri.`, ` autorizzata dal Ministro degli Affari Esteri.` ] , 
      "4508" : [ `A quale organo compete, ai sensi dell'art. 80 della Costituzione, l'autorizzazione alla ratifica di trattati internazionali che importano oneri alle finanze?`, `Camere, con legge.`, `Ministro dell'economia e delle finanze.`, `Corte dei Conti.` ] , 
