      "4515" : [ `Lo Stato assicura l'equilibrio tra le entrate e le spese del proprio bilancio, tenendo conto delle fasi avverse e delle fasi favorevoli del ciclo economico. La Costituzione italiana ammette il ricorso all'indebitamento?`, `Si, ma alle condizioni espressamente previste all'art. 81 Cost.`, `No, il ricorso all'indebitamento  espressamente vietato dall'art. 81 Cost.`, `Si, la costituzione ammette in ogni caso forme di indebitamento.` ] , 
