      "4546" : [ `Quale delle indicazioni che seguono  conforme alle prescrizioni dell'art. 81 della Costituzione in materia di bilancio dello Stato?`, `Il rendiconto consuntivo dello Stato  approvata dalle Camere.`, `La sostenibilit del debito del complesso delle pubbliche amministrazioni  stabilita con provvedimenti del Ministro dell'economia e delle finanze.`, `Il ricorso all'indebitamento  autorizzato dalla Presidenza del Consiglio dei Ministri.` ] , 
      "4547" : [ `Quale delle indicazioni che seguono  conforme alle prescrizioni dell'art. 81 della Costituzione in materia di bilancio dello Stato?`, `Il bilancio dello Stato  approvato annualmente con legge.`, `L'esercizio provvisorio del bilancio pu essere concesso solo con provvedimento del Presidente della Repubblica.`, `Il ricorso all'indebitamento  consentito previa autorizzazione del Ministro dell'economia e delle finanze.` ] , 
      "4548" : [ `Quale delle indicazioni che seguono  conforme alle prescrizioni dell'art. 81 della Costituzione in materia di bilancio dello Stato?`, `Il rendiconto consultivo dello Stato  approvato annualmente con legge.`, `L'esercizio provvisorio non pu essere concesso per periodi superiori complessivamente a otto mesi.`, `Il ricorso all'indebitamento  autorizzato dal Ministro dello sviluppo economico.` ] , 
      "4549" : [ `Quale delle indicazioni che seguono  conforme alle prescrizioni dell'art. 81 della Costituzione in materia di bilancio dello Stato?`, `L'esercizio provvisorio del bilancio non pu essere concesso se non per legge e per periodi non superiori complessivamente a quattro mesi.`, `Il bilancio dello Stato  presentato al Parlamento dalla Corte dei conti.`, `Il contenuto della legge di bilancio  stabilito con provvedimenti del Ministro dello sviluppo economico.` ] , 
      "4550" : [ `Quale delle indicazioni che seguono  conforme alle prescrizioni dell'art. 81 della Costituzione in materia di bilancio dello Stato?`, `L'esercizio provvisorio del bilancio pu essere concesso solo per legge.`, `Il bilancio dello Stato  approvato dal Governo.`, `Le norme fondamentali e i criteri volti ad assicurare l'equilibrio tra le entrate e le spese dei bilanci sono stabiliti con provvedimenti del Ministro dello sviluppo economico.` ] , 
      "4551" : [ `Quale delle indicazioni che seguono  conforme alle prescrizioni dell'art. 81 della Costituzione in materia di bilancio dello Stato?`, `L'esercizio provvisorio non pu essere concesso per periodi superiori complessivamente a quattro mesi.`, `Il bilancio dello Stato  approvato dalla Corte dei conti.`, `Il ricorso all'indebitamento  consentito previa autorizzazione del Governo.` ] , 
      "4552" : [ `Quale delle indicazioni che seguono  conforme alle prescrizioni dell'art. 81 della Costituzione in materia di bilancio dello Stato?`, `Il contenuto della legge di bilancio  stabilito con legge approvata dalle Camere.`, `Il rendiconto consuntivo dello Stato  approvato dal Governo.`, `Il ricorso all'indebitamento  autorizzato dal Ministro dello sviluppo economico.` ] , 
      "4553" : [ `Quale delle indicazioni che seguono  conforme alle prescrizioni dell'art. 81 della Costituzione in materia di bilancio dello Stato?`, `Le norme fondamentali e i criteri volti ad assicurare l'equilibrio tra le entrate e le spese dei bilanci sono stabiliti con legge approvata dalle Camere.`, `Il rendiconto consuntivo dello Stato  approvato dalla Ragioneria generale dello Stato.`, `L'esercizio provvisorio non pu essere concesso per periodi superiori complessivamente a due mesi.` ] , 
      "4554" : [ `Quale delle indicazioni che seguono  conforme alle prescrizioni dell'art. 81 della Costituzione in materia di bilancio dello Stato?`, `La sostenibilit del debito del complesso delle pubbliche amministrazioni  stabilito con legge approvata dalle Camere.`, `Il bilancio dello Stato  approvato annualmente con decreto legislativo.`, `Il ricorso all'indebitamento  autorizzato dalla Presidenza del Consiglio dei Ministri.` ] , 
      "4555" : [ `Quale delle indicazioni che seguono  conforme alle prescrizioni dell'art. 81 della Costituzione in materia di bilancio dello Stato?`, `Ogni altra legge che importi nuove e maggiori spese deve indicare i mezzi per farvi fronte.`, `Dopo l'approvazione del bilancio non  possibile approvare leggi che importino nuove e maggiori spese.`, `L'esercizio provvisorio del bilancio non pu essere concesso se non per legge e per periodi non superiori complessivamente a due mesi.` ] , 
      "4556" : [ `Nella maggior parte degli ordinamenti democratici la modifica di una norma costituzionale pu avvenire solo con maggioranze pi elevate e procedure pi complesse rispetto a quelle legislative ordinarie, in quanto la Costituzione contiene le norme e i principi fondamentali della vita della collettivit. Questi ordinamenti, tra cui quello italiano, vengono definiti anche:`, `"A Costituzione rigida."`, `"A Costituzione scritta."`, `"A Costituzione flessibile."` ] , 
      "4557" : [ `La Costituzione rigida:`, ` modificabile solo a mezzo di leggi emanate con procedimenti particolari.`, `Si limita a stabilire e regolare l'organizzazione politica e amministrativa dello Stato.`, ` consacrata in un documento formale.` ] , 
      "4558" : [ `Dato che lo statuto albertino poteva essere modificato dalle leggi ordinarie, il regime fascista riusc facilmente ad aggirarne le norme. Preoccupata che potessero nuovamente verificarsi simili esperienze, l'Assemblea costituente decise di rendere la nostra Costituzione:`, `Rigida.`, `Flessibile.`, `Immodificabile.` ] , 
      "4559" : [ `L'esigenza di predisporre un apposito organo in grado di sindacare l'operato del legislatore ordinario  frutto dell'introduzione di una Costituzione di tipo:`, `Rigido.`, `Flessibile.`, `Lungo.` ] , 
      "4560" : [ `La Costituzione flessibile:`, ` modificabile a mezzo degli ordinari strumenti legislativi.`, `Comprende le norme sull'organizzazione statale ed i principi fondamentali dello Stato, nonch i diritti fondamentali dei cittadini.`, ` fondata solo su principi tramandati consuetudinariamente.` ] , 
      "4561" : [ `Una Costituzione pu essere ottriata. Cosa significa?`, `"Che  concessa unilateralmente per grazia del Sovrano."`, `Che  volontariamente e liberamente adottata dal popolo, attraverso un apposito organo.`, `Che  fondata solo su consuetudini.` ] , 
      "4562" : [ `La Costituzione votata:`, ` adottata volontariamente e liberamente dal popolo, attraverso un apposito organo.`, ` modificabile a mezzo degli ordinari strumenti legislativi.`, `Comprende le norme sull'organizzazione statale ed i principi fondamentali dello Stato, nonch i diritti fondamentali dei cittadini.` ] , 
      "4563" : [ `La Costituzione non scritta:`, ` fondata solo su principi tramandati consuetudinariamente.`, ` adottata volontariamente e liberamente dal popolo, attraverso un apposito organo.`, `Non  modificabile a mezzo degli ordinari strumenti legislativi.` ] , 
