      "4701" : [ `Il diritto di libert di cui all'art. 16 della Costituzione si estrinseca in tre facolt, ovvero:`, `Libera circolazione nel territorio dello Stato, libert di fissare ovunque la propria residenza, libert di uscire e di rientrare nello Stato.`, `Libera circolazione nel territorio dello Stato, libert di fissare ovunque la propria residenza, libert di uscire dallo Stato ma non di rientrarvi.`, `Libera circolazione nel territorio dello Stato, libert di fissare ovunque la propria residenza, libert di uscire definitivamente dallo Stato.` ] , 
