      "4706" : [ `Il diritto di libert per il cittadino di fissare ovunque la propria residenza  espressamente riconosciuto dalla Costituzione?`, `Si, all'art. 16.`, `Si, tale diritto  riconosciuto anche agli apolidi e agli stranieri.`, `Si, tale diritto  riconosciuto a chiunque si trovi a qualunque titolo nel territorio dello Stato.` ] , 
