      "4708" : [ `"Con riferimento ai diritti e alla libert fondamentali cosa si intende per libert di stabilimento?"`, `Il diritto per i cittadini dell'Unione europea di svolgere senza restrizioni, dovute alla loro diversa cittadinanza, attivit lavorativa in ciascuno degli Stati membri.`, `Il diritto per i cittadini dell'Unione europea di circolare liberamente, senza restrizioni, dovute alla loro diversa cittadinanza, in ciascuno degli Stati membri.`, `Il diritto per i cittadini dell'Unione europea di fissare la propria residenza, senza restrizioni dovute alla loro diversa cittadinanza, in ciascuno degli Stati membri.` ] , 
      "4709" : [ `L'art. 16 della Costituzione stabilendo che ogni cittadino pu circolare liberamente in qualsiasi parte del territorio nazionale, salvo le limitazioni che la legge stabilisce in via generale per motivi di sanit o di sicurezza contiene:`, `Una riserva di legge rinforzata.`, `Una riserva di legge costituzionale.`, `Una riserva di legge formale.` ] , 
      "4710" : [ `L'art. 16 della Costituzione dispone che ogni cittadino pu circolare liberamente in qualsiasi parte del territorio nazionale, salvo le limitazioni che la legge stabilisce in via generale per motivi di sanit o di sicurezza. Sono da considerare motivi di sanit:`, `Quelli relativi alla salute pubblica.`, `Quelli relativi alla salute personale.`, `Sia quelli relativi alla salute pubblica sia quelli relativi alla salute personale.` ] , 
      "4711" : [ `L'art. 16 della Costituzione dispone che ogni cittadino pu circolare liberamente in qualsiasi parte del territorio nazionale, salvo le limitazioni che la legge stabilisce in via generale per motivi di sanit o di sicurezza. Sono da considerare motivi di sicurezza:`, `Quelli che riguardano i pericoli relativi all'incolumit fisica o all'ordine pubblico.`, `Solo quelli che riguardano i pericoli relativi all'incolumit fisica.`, `Solo quelli che riguardano i pericoli relativi all'ordine pubblico.` ] , 
      "4712" : [ `Una regione pu adottare provvedimenti che possano ostacolare la libera circolazione delle persone e delle cose tra le Regioni?`, `No, per espressa previsione di cui all'art. 120 Cost.`, `Si, per espressa previsione di cui all'art. 120 Cost.`, `Una Regione pu adottare solo provvedimenti che possano ostacolare la libera circolazione delle cose tra le Regioni.` ] , 
