      "4718" : [ `Con riferimento alla libert di circolazione, soggiorno e espatrio, cosa specifica l'art. 16 della Costituzione?`, `Che ogni cittadino  libero di uscire dal territorio della Repubblica e di rientrarvi, salvo gli obblighi di legge.`, `Che chiunque pu circolare liberamente in qualsiasi parte del territorio nazionale, salvo le limitazioni che la legge stabilisce in via generale per motivi di sanit o di sicurezza.`, `Che sono ammesse restrizione determinate da ragioni politiche.` ] , 
      "4719" : [ `Indicare quale affermazione sulla libera circolazione, soggiorno ed espatrio non  correttamente riferibile al disposto di cui all'art. 16 della Costituzione.`, `Che chiunque pu circolare liberamente in qualsiasi parte del territorio nazionale, salvo le limitazioni che la legge stabilisce in via generale per motivi di sanit o di sicurezza.`, `Che ogni cittadino pu soggiornare liberamente in qualsiasi parte del territorio nazionale, salvo le limitazioni che la legge stabilisce in via generale per motivi di sanit o di sicurezza.`, `Che nessuna restrizione pu essere determinata da ragioni politiche.` ] , 
      "4720" : [ `Indicare quale affermazione sulla libera circolazione, soggiorno ed espatrio non  correttamente riferibile al disposto di cui all'art. 16 della Costituzione.`, `Che ogni cittadino pu soggiornare liberamente in qualsiasi parte del territorio nazionale, senza alcuna limitazione.`, `Che ogni cittadino pu circolare liberamente in qualsiasi parte del territorio nazionale, salvo le limitazioni che la legge stabilisce in via generale per motivi di sanit o di sicurezza.`, `Che nessuna restrizione pu essere determinata da ragioni politiche.` ] , 
      "4721" : [ `Indicare quale affermazione sulla libera circolazione, soggiorno ed espatrio non  correttamente riferibile al disposto di cui all'art. 16 della Costituzione.`, `Che sono ammesse restrizione determinate da ragioni politiche.`, `Che ogni cittadino pu circolare liberamente in qualsiasi parte del territorio nazionale, salvo le limitazioni che la legge stabilisce in via generale per motivi di sanit o di sicurezza.`, `Che ogni cittadino  libero di uscire dal territorio della Repubblica e di rientrarvi, salvo gli obblighi di legge.` ] , 
      "4722" : [ `Indicare quale affermazione sulla libera circolazione, soggiorno ed espatrio non  correttamente riferibile al disposto di cui all'art. 16 della Costituzione.`, `Che chiunque  libero di uscire dal territorio della Repubblica e di rientrarvi.`, `Che ogni cittadino pu soggiornare liberamente in qualsiasi parte del territorio nazionale, salvo le limitazioni che la legge stabilisce in via generale per motivi di sanit o di sicurezza.`, `Che nessuna restrizione pu essere determinata da ragioni politiche.` ] , 
      "4723" : [ `A norma di quanto dispone l'art. 16 della Costituzione, la libert di soggiorno pu essere limitata?`, `No, salvo i casi previsti dalla legge.`, `No, tranne che per motivi politici.`, `Si, sempre.` ] , 
      "4724" : [ `A norma di quanto dispone l'art. 16 della Costituzione, il cittadino italiano  libero di uscire dal territorio della Repubblica e di rientrarvi?`, `Si, salvo gli obblighi di legge.`, `Si, senza alcuna limitazione.`, `Si, previa autorizzazione del Comune di residenza.` ] , 
