      "4727" : [ `"Ai sensi dell'art. 16 della Costituzione le limitazioni alla libert di circolazione e soggiorno possono essere stabilite esclusivamente in due casi; uno di detti casi  per motivi:"`, `Di sicurezza.`, `Economici.`, `Fiscali.` ] , 
      "4728" : [ `La libert di riunione  garantita ai cittadini dall'art. 17 della Costituzione. Si usa il termine assembramenti:`, `Per indicare, di norma, riunioni occasionali causate da una circostanza improvvisa ed imprevista.`, `Per indicare, di norma, riunioni in movimento dove l'identit del luogo  puramente relativa a soggetti riuniti e compresenti nello stesso spazio, sia che rimangono fermi, sia che si muovono.`, `Per indicare tutte le riunioni che si svolgono senza la preventiva autorizzazione del questore, quindi non autorizzate.` ] , 
      "4729" : [ `La libert di riunione  garantita ai cittadini dall'art. 17 della Costituzione. Si usa il termine dimostrazioni:`, `Per indicare, di norma, riunioni che danno luogo a manifestazioni per scopi civili o politici.`, `Per indicare, di norma, una riunione occasionale causata da una circostanza improvvisa ed imprevista.`, `Per indicare le sole riunioni organizzate dai partiti politici o dalle organizzazioni sindacali.` ] , 
      "4730" : [ `La libert di riunione  garantita ai cittadini dall'art. 17 della Costituzione. Si usa il termine cortei:`, `Per indicare, di norma, riunioni in movimento dove l'identit del luogo  puramente relativa a soggetti riuniti e compresenti nello stesso spazio, sia che rimangano fermi, sia che si muovano.`, `Per indicare, di norma, una riunione occasionale causata da una circostanza improvvisa ed imprevista.`, `Per indicare, di norma, riunioni che danno luogo a manifestazioni per scopi civili o politici.` ] , 
      "4731" : [ `L'art. 17 della Costituzione afferma il diritto dei cittadini a riunirsi liberamente purch pacificamente e senza armi. Le riunioni a seconda del luogo in cui si svolgono si distinguono in:`, `Private, aperte al pubblico e pubbliche.`, `Private e pubbliche.`, `Sindacali o private.` ] , 
      "4732" : [ `La disciplina costituzionale, in merito al diritto di riunione dei cittadini, distingue tra luoghi aperti al pubblico, luoghi pubblici e luoghi privati. Sono privati:`, `Quei luoghi riservati ad un uso privato da parte di soggetti determinati che ne dispongono, in quanto proprietari o per altro titolo legittimo in modo esclusivo.`, `Quei luoghi materialmente chiusi o comunque, separati dall'esterno, in cui il libero accesso del pubblico  subordinato all'osservanza di determinate modalit e condizioni poste da chi di quel luogo ha la disponibilit.`, `Tutti i luoghi in cui le persone possono accedere senza alcuna condizione o modalit particolare.` ] , 
      "4733" : [ `La disciplina costituzionale, in merito al diritto di riunione dei cittadini, distingue espressamente tra luoghi aperti al pubblico e luoghi pubblici. Sono aperti al pubblico:`, `Quei luoghi materialmente chiusi o comunque, separati dall'esterno, in cui il libero accesso del pubblico  subordinato all'osservanza di determinate modalit e condizioni poste da chi di quel luogo ha la disponibilit.`, `Quei luoghi riservati ad un uso privato da parte di soggetti determinati che ne dispongono, in quanto proprietari o per altro titolo legittimo in modo esclusivo.`, `Tutti i luoghi in cui le persone possono accedere senza alcuna condizione o modalit particolare.` ] , 
      "4734" : [ `La disciplina costituzionale, in merito al diritto di riunione dei cittadini, distingue espressamente tra luoghi aperti al pubblico e luoghi pubblici. Sono luoghi pubblici:`, `Quelli ammessi al libero transito delle persone come vie e piazze.`, `Quei luoghi materialmente chiusi o comunque, separati dall'esterno, in cui il libero accesso del pubblico  subordinato all'osservanza di determinate modalit e condizioni poste da chi di quel luogo ha la disponibilit.`, `Solo le sedi e gli uffici della pubblica amministrazione.` ] , 
      "4735" : [ `Ai fini delle libert di riunione una sala cinematografica  considerata:`, `Luogo aperto al pubblico.`, `Luogo privato.`, `Luogo pubblico.` ] , 
      "4736" : [ `Le riunioni in luogo pubblico possono essere vietate, ai sensi della Costituzione:`, `Per comprovati motivi di sicurezza o di incolumit pubblica.`, `Per evitare ostacoli alla circolazione.`, `Solo se non autorizzate.` ] , 
      "4737" : [ `Quale tra le seguenti affermazioni corrisponde ad un principio sancito dalla Costituzione italiana, in merito ai rapporti civili?`, `I cittadini hanno diritto di riunirsi pacificamente e senza armi.`, `Per le riunioni, anche in luogo pubblico, non  richiesto preavviso.`, `Per le riunioni private e in luogo aperto al pubblico occorre l'assenso dell'autorit di pubblica sicurezza.` ] , 
      "4738" : [ `A norma di quanto stabilito all'art. 17 della Costituzione, i cittadini possono riunirsi liberamente?`, `Si, ma pacificamente e senz'armi.`, `Si, senza limitazione alcuna.`, `Si, previa autorizzazione.` ] , 
      "4739" : [ `A norma di quanto dispone l'art. 17 della Costituzione, in quale dei seguenti casi non possono tenersi riunioni, senza preavviso alle autorit?`, `In caso di riunioni in luogo pubblico.`, `In caso di riunioni in luogo aperto al pubblico.`, `Nessuna delle altre risposte  corretta. La Costituzione nulla dispone sulle riunioni dei cittadini.` ] , 
