      "4752" : [ `"L'art. 17 della Costituzione riconosce il diritto inviolabile della libert di riunione; una riunione in luogo pubblico pu essere vietata?"`, `Si, soltanto per comprovati motivi di sicurezza o di incolumit pubblica.`, `Si, come le riunioni private.`, `No, per espressa previsione costituzionale.` ] , 
