      "4760" : [ `Con riferimento alla tutela delle formazioni sociali la costituzione all'art. 18 riconosce il diritto di libert di associazione, specificando che:`, `Sono proibite le associazioni che perseguono, anche indirettamente, scopi politici mediante organizzazioni di carattere militare.`, `I cittadini hanno diritto di associarsi liberamente, previa autorizzazione dell'autorit competente, per fini che non sono vietati ai singoli dalla legge penale .`, `Sono ammesse le associazioni segrete.` ] , 
      "4761" : [ `La Costituzione italiana oltre ad aver garantito in linea generale (art. 18) la libert di associazione, riconosce espressamente tale libert nel campo politico?`, `Si, all'art. 49.`, `No, riconosce espressamente tale libert solo nel campo sindacale.`, `No, riconosce espressamente tale libert solo nel campo religioso.` ] , 
