      "4765" : [ `La libert di associazione riconosciuta all'art. 18 della Costituzione si sostanzia anche nella libert di aderire ad una associazione?`, `Si.`, `No, si sostanzia solo nella possibilit di costituire una associazione.`, `No, si sostanzia solo nella possibilit di recedere da una associazione.` ] , 
