      "4769" : [ `Quale tra le seguenti affermazioni corrisponde ad un principio sancito dalla Costituzione italiana, in materia di diritti e doveri dei cittadini?`, `Sono proibite le associazioni segrete.`, `Sono ammesse le associazioni segrete, purch non abbiano scopi di carattere militare.`, `Sono ammesse le associazioni che perseguono scopi politici mediante organizzazioni di carattere militare, previa autorizzazione del Questore.` ] , 
      "4770" : [ `Quale tra le seguenti affermazioni corrisponde ad un principio sancito dalla Costituzione italiana, in materia di diritti e doveri dei cittadini?`, `Sono proibite le associazioni che perseguono, anche indirettamente, scopi politici mediante organizzazioni di carattere militare.`, `Sono ammesse le associazioni segrete, purch non abbiano scopi di carattere militare.`, `Sono ammesse le associazioni che perseguono scopi politici mediante organizzazioni di carattere militare, previa autorizzazione del Questore.` ] , 
