      "4805" : [ `Con riferimento a quanto dispone l'art. 14 della Costituzione sulla libert del domicilio, gli accertamenti compiuti dall'Autorit amministrativa nel domicilio di una persona a fini economici e fiscali sono regolati da:`, `Leggi speciali.`, `Provvedimenti dell'Autorit giudiziaria.`, `Regolamenti del Ministero della Sanit.` ] , 
      "4806" : [ `L'inviolabilit della corrispondenza  statuita:`, `Dalla Costituzione all'art. 15.`, `Esclusivamente dalla legge ordinaria.`, `Dalla consuetudine.` ] , 
      "4807" : [ `L'art. 15 della Costituzione, nell'affermare che la libert e la segretezza della corrispondenza e di ogni altra forma di comunicazione sono inviolabili, intende salvaguardare le comunicazioni indirizzate:`, `A persone determinate.`, `Al pubblico.`, `Ad una collettivit indistinta.` ] , 
      "4808" : [ `L'art. 15 della Costituzione tutela il rapporto comunicativo che si instaura tra due soggetti determinati:`, `Sancendo il principio inviolabile della libert, della segretezza della corrispondenza e di ogni altra forma di comunicazione.`, `Sancendo il principio della libert, della segretezza riferite alla corrispondenza epistolare.`, `Della violabilit, della segretezza della corrispondenza e di ogni altra forma di comunicazione.` ] , 
      "4809" : [ `A norma del disposto di cui all'art. 15 della Costituzione:`, `Sono inviolabili la libert e la segretezza della corrispondenza e di ogni altra forma di comunicazione.`, ` inviolabile la segretezza della corrispondenza e di ogni altra forma di comunicazione.`, ` inviolabile la libert della corrispondenza e di ogni altra forma di comunicazione.` ] , 
      "4810" : [ `La tutela di cui all'art. 15 della Costituzione circa l'inviolabilit della libert e della segretezza della corrispondenza:`, `Riguarda tutte le forme di corrispondenza: epistolare, telegrafica e telefonica.`, `Non riguarda la corrispondenza telefonica e telegrafica.`, `Riguarda solo la corrispondenza epistolare e telegrafica.` ] , 
      "4811" : [ `La titolarit della libert di corrispondenza sancita all'art. 15 della Costituzione spetta:`, `A tutti: cittadini, stranieri, apolidi, persone fisiche, persone giuridiche, formazioni sociali.`, `Ai cittadini, stranieri, apolidi nonch a tutte le persone fisiche.`, `Ai cittadini italiani e ai cittadini europei.` ] , 
      "4812" : [ `A chi si rivolge la Costituzione quando all'art. 15 sancisce che: La libert e la segretezza della corrispondenza e di ogni altra forma di comunicazione sono inviolabili?`, `A tutti.`, `Ai cittadini italiani.`, `Alle persone giuridiche e fisiche.` ] , 
      "4813" : [ `La titolarit della libert di corrispondenza sancita all'art. 15 della Costituzione spetta anche alle persone giuridiche?`, `Si, spetta a tutti.`, `No, spetta solo ai cittadini, agli stranieri e agli apolidi.`, `No, spetta solo ai cittadini italiani e ai cittadini europei.` ] , 
      "4814" : [ `La titolarit della libert di corrispondenza sancita all'art. 15 della Costituzione spetta anche alle formazioni sociali?`, `Si, spetta a tutti.`, `No, spetta solo ai cittadini, agli stranieri e agli apolidi.`, `No, spetta solo ai cittadini italiani e ai cittadini europei.` ] , 
      "4815" : [ `A norma del disposto di cui all'art. 15 della Costituzione la limitazione della libert e della segretezza della corrispondenza:`, `Deve essere accompagnata da idonee forme di garanzia e solo con atto motivato dall'Autorit giudiziaria.`, `Pu essere disposta anche dall'Autorit amministrativa.`, `Non necessita di particolari garanzie.` ] , 
      "4816" : [ `La libert e la segretezza della corrispondenza e di ogni altra forma di comunicazione sono inviolabili. La loro limitazione pu avvenire                                con le garanzie stabilite dalla legge. Completare con l'opzione corretta.`, `Soltanto per atto motivato dell'Autorit giudiziaria.`, `Sia per atto motivato dell'Autorit giudiziaria che dell'Autorit amministrativa.`, `Soltanto per atto motivato dell'Autorit amministrativa.` ] , 
      "4817" : [ `L'atto con cui l'Autorit giudiziaria limita il diritto alla libert e alla segretezza della corrispondenza e di ogni altra forma di comunicazione deve essere motivato?`, `Si, per espressa previsione di cui all'art. 15 della Costituzione e deve avvenire con le garanzie stabilite dalla legge.`, `No, non necessita di motivazione.`, `No, solo l'atto con cui l'Autorit amministrativa limita il diritto alla libert e alla segretezza della corrispondenza deve essere motivato.` ] , 
      "4818" : [ `Quali sono, tra i seguenti, i destinatari del precetto costituzionale sancito dall'art. 19 in materia di libert religiosa?`, `Tutti.`, `Tutti, ad eccezione degli apolidi.`, `Tutti, ad eccezione dei rifugiati.` ] , 
      "4819" : [ `Quale delle seguenti affermazioni  consona al disposto normativo dell'art. 19 della Costituzione sulla libert religiosa?`, `Tutti hanno diritto di professare liberamente la propria fede religiosa, sia in forma individuale che associata, di farne propaganda e di esercitarne in privato o in pubblico il culto, purch non si tratti di riti contrari al buon costume.`, `Tutti hanno diritto di professare liberamente la propria fede religiosa in forma individuale ma non associata, di farne propaganda e di esercitarne in pubblico il culto, purch non si tratti di riti contrari al buon costume.`, `"Tutti hanno diritto di professare liberamente la propria fede religiosa, sia in forma individuale che associata, di farne propaganda e di esercitarne in privato ma non in pubblico il culto, purch non si tratti di riti contrari al buoncostume."` ] , 
      "4820" : [ `Quale delle seguenti libert non si evince dal disposto normativo dell'art. 19 della Costituzione sulla libert religiosa?`, `La libert di disporre liberamente del proprio corpo per motivi religiosi.`, `La libert di fede qualunque sia, compreso l'ateismo.`, `La libert di culto sia in privato che in luogo pubblico.` ] , 
      "4821" : [ `Quale limite incontra la libert di fede religiosa sancita all'art. 19 della Costituzione?`, `Il fatto che i riti e gli atti di culto non devono essere contrari al buon costume.`, `Il fatto che  fatto divieto di divulgare con opere di proselitismo la propria fede religiosa.`, `Nessun limite.` ] , 
      "4822" : [ `                       hanno diritto di professare liberamente la propria fede religiosa in qualsiasi forma, individuale o associata, di farne propaganda e di esercitarne in privato o in pubblico il culto, purch non si tratti di riti contrari al buon costume. Completare con l'opzione corretta.`, `Tutti.`, `I cittadini.`, `I cittadini e gli apolidi.` ] , 
      "4823" : [ `Tutti hanno diritto di professare liberamente la propria fede religiosa in qualsiasi forma, individuale o associata, di farne propaganda e di esercitarne in privato o in pubblico il culto,                            . Completare con l'opzione corretta.`, `Purch non si tratti di riti contrari al buon costume.`, `Anche se si tratta di riti contrari al buon costume.`, `Purch sia riconosciuta dallo Stato italiano.` ] , 
      "4824" : [ `A norma dell'art. 20 della Costituzione, il carattere ecclesiastico ed il fine di religione di una associazione:`, `Non possono essere causa di speciali limitazioni legislative.`, `Possono comportare l'applicazione di speciali aggravi fiscali per la sua costituzione.`, `Possono essere causa di limitazione della capacit giuridica.` ] , 
