      "4829" : [ `Il carattere ecclesiastico e il fine di religione o di culto d'una associazione od istituzione possono essere causa di speciali gravami fiscali per la sua capacit giuridica?`, `No, per espressa previsione di cui all'art. 20 della Costituzione.`, `Si, per espressa previsione di cui all'art. 20 della Costituzione.`, `Si, e possono essere causa anche di speciali limitazioni legislative.` ] , 
