      "4831" : [ `L'art. 21 della Costituzione tutela, nell'ambito della libert di manifestazione del pensiero, le comunicazioni pubbliche rivolte:`, `Ad una collettivit indistinta.`, `A persone determinate.`, `Ad una persona determinata.` ] , 
