      "4833" : [ `A norma di quanto dispone l'art. 21 della Costituzione, la stampa pu essere soggetta a censure?`, `No, la stampa non pu essere soggetta ad autorizzazioni o censure.`, `Si.`, `Pu essere soggetta solo ad autorizzazioni.` ] , 
