      "4847" : [ `Quale dei seguenti principi non  sancito dall'art. 21 della Costituzione sulla libert di stampa?`, `Divieto dei controlli sui mezzi di finanziamento della stampa periodica.`, `Esclusione di qualsiasi forma di autorizzazione preventiva.`, `Previsione della disciplina legislativa relativa alle ipotesi di sequestro dello stampato.` ] , 
      "4848" : [ `Dispone l'art. 22 della Costituzione che nessuno pu essere privato, per motivi politici,:`, `Della capacit giuridica, della cittadinanza, del nome.`, `Della capacit giuridica e della cittadinanza.`, `Della capacit giuridica e del nome.` ] , 
