      "4852" : [ `A norma della Costituzione, i cittadini possono agire in giudizio per la tutela dei propri interessi legittimi?`, `Si.`, `No, possono agire soltanto per la tutela dei diritti soggettivi.`, `Nessuna delle altre risposte  corretta. La Costituzione nulla dispone in merito.` ] , 
      "4853" : [ `Secondo la Costituzione, la difesa in giudizio  un diritto inviolabile:`, `In ogni stato e grado del procedimento.`, `In ogni stato del procedimento, ma solo nei primi due gradi.`, `Solamente nel giudizio di primo grado.` ] , 
      "4854" : [ `"L'art. 24 della Costituzione sancisce il diritto alla tutela giurisdizionale ed afferma l'universalit e l'inviolabilit del diritto alla difesa; in quale dei seguenti casi tale diritto pu essere limitato?"`, `Mai.`, `Norme di diritto internazionale consuetudinario.`, `Leggi di revisione della Costituzione.` ] , 
      "4855" : [ `Il principio del diritto al giudice naturale contenuto nell'art. 25, comma 1, della Costituzione significa che:`, `La competenza del giudice deve essere predeterminata dalla legge in base a criteri oggettivi.`, `La competenza del giudice deve essere determinata dalla legge nel momento in cui viene commesso il fatto.`, `La competenza del giudice deve essere determinata dalla legge nel momento del suo esercizio concreto.` ] , 
      "4856" : [ `Il principio costituzionale (art. 25) della irretroattivit della legge penale significa che:`, `Nessuno pu essere punito se non in forza di una legge che sia entrata in vigore prima del fatto commesso, salvo il favor rei, ossia l'applicazione di una legge precedente pi favorevole al reo purch non sia intervenuta sentenza irrevocabile di condanna.`, `Nessuno pu essere punito se non in forza di una legge che sia entrata in vigore prima del fatto commesso anche se la legge precedente era pi favorevole al reo.`, `Nessuno pu essere punito se non in forza di una legge che sia entrata in vigore prima del fatto commesso, salvo il favor rei, ossia l'applicazione di una legge precedente pi favorevole al reo anche se  intervenuta sentenza irrevocabile di condanna.` ] , 
      "4857" : [ `A norma della Costituzione, i cittadini possono essere distolti dal giudice naturale precostituito per legge?`, `No, mai.`, `Si, nei casi previsti dalla legge ordinaria.`, `Si, in quanto la scelta del giudice rappresenta un diritto inviolabile costituzionalmente garantito.` ] , 
      "4858" : [ `In quali casi i cittadini possono essere sottoposti a misure di sicurezza?`, `Nei casi previsti dalla legge.`, `In tutti i casi in cui l'Autorit giudiziaria lo ritenga necessario.`, `Quando sussistano lievi indizi di colpevolezza.` ] , 
      "4859" : [ `Il principio costituzionale del divieto di estradizione per reati politici (art. 26) si applica:`, `Delitti puniti per discriminatorie finalit di persecuzione politica.`, `Delitti di terrorismo.`, `Delitti di genocidio.` ] , 
