      "4906" : [ `Dispone l'art. 38 della Costituzione che i lavoratori hanno diritto che siano previsti ed assicurati mezzi adeguati alle loro esigenze di vita. Ci:`, `In caso di disoccupazione involontaria.`, `In ogni caso.`, `Solo in caso di malattia.` ] , 
