      "4908" : [ `Dispone l'art. 38 della Costituzione che i lavoratori hanno diritto che siano previsti ed assicurati mezzi adeguati alle loro esigenze di vita. Ci:`, `In caso di invalidit e vecchiaia.`, `In ogni caso.`, `Solo in caso di disoccupazione involontaria.` ] , 
      "4909" : [ `Dispone l'art. 38 della Costituzione che i lavoratori hanno diritto che siano previsti ed assicurati mezzi adeguati alle loro esigenze di vita. Ci:`, `In caso di malattia.`, `In ogni caso.`, `Solo in caso di vecchiaia.` ] , 
      "4910" : [ `A norma della Costituzione, l'organizzazione sindacale :`, `Libera.`, `Sottoposta al controllo degli organi competenti per legge.`, `Soggetta ad autorizzazione governativa per l'esercizio dei suoi poteri.` ] , 
      "4911" : [ `La Costituzione della Repubblica italiana riconosce ai lavoratori il diritto di sciopero?`, `Si, all'art. 40.`, `No, soltanto il Codice Civile riconosce il diritto di sciopero.`, `Si, all'art. 2.` ] , 
