      "4915" : [ `A norma della Costituzione, a chi possono appartenere i beni economici?`, `Allo Stato, ad enti o a privati.`, `Esclusivamente allo Stato e agli enti.`, `Esclusivamente allo Stato e ai privati.` ] , 
      "4916" : [ `Ai sensi dell'art. 42 della Costituzione, per quali dei seguenti motivi la propriet privata pu essere espropriata?`, `Per motivi di interesse generale, nei casi previsti dalla legge.`, `Per motivi di interesse privato.`, `Nessuna delle altre risposte  corretta. La costituzione vieta l'espropriazione.` ] , 
      "4917" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, ` compito delle associazioni di categoria dei lavoratori e delle associazioni datoriali curare la formazione e l'elevazione professionale dei lavoratori.`, `La durata massima della giornata lavorativa  stabilita dalla legge.`, `Tutela delle lavoratrici madri.` ] , 
