      "4919" : [ `La Costituzione sancisce espressamente il diritto per il lavoratore ad avere una retribuzione proporzionata alla quantit e qualit del suo lavoro?`, `Si, all'art. 36, comma 1.`, `No, la costituzione nulla dispone in merito alla retribuzione.`, `No, la costituzione sancisce solo il diritto ad avere una retribuzione.` ] , 
      "4920" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Rinunciabilit alle ferie annuali, anche senza compenso.`, `Determinazione della durata massima della giornata lavorativa stabilita dalla legge.`, `Il diritto ad una retribuzione proporzionata alla quantit e qualit del lavoro.` ] , 
      "4921" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Rinunciabilit al riposo settimanale, anche senza compenso.`, `Il diritto ad una retribuzione proporzionata alla quantit e qualit del lavoro.`, `Determinazione dell'et minima per poter svolgere lavoro salariato stabilita dalla legge.` ] , 
      "4922" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `La durata massima della giornata lavorativa  stabilita dai C.C.N.L.`, `Il riposo settimanale  irrinunciabile.`, `L'uguaglianza dei diritti tra lavoratore e lavoratrici.` ] , 
      "4923" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `L'et minima per poter svolgere lavoro salariato  stabilita dai C.C.N.L.`, `Le ferie annuali retribuite sono irrinunciabili.`, `Il diritto ad una retribuzione proporzionata alla quantit e qualit del lavoro.` ] , 
      "4924" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Assoluto divieto di lavoro per i minori.`, `Parit di retribuzione tra uomini e donne a parit di lavoro.`, `Il diritto ad una retribuzione proporzionata alla quantit e qualit del lavoro.` ] , 
      "4925" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Comprimibilit dei diritti e delle retribuzioni in caso di maternit al fine di salvaguardare i livelli di produzione.`, `Irrinunciabilit del lavoratore alle ferie annuali retribuite.`, `Il diritto all'educazione ed all'avviamento professionale per coloro che sono inabili al lavoro.` ] , 
      "4926" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Determinazione della retribuzione per il lavoro dei minori stabilita dai C.C.N.L.`, `Parit di retribuzione tra uomini e donne a parit di lavoro.`, `Determinazione della durata massima della giornata lavorativa stabilita per legge.` ] , 
      "4927" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Il diritto all'educazione ed all'avviamento professionale per le sole persone abili al lavoro.`, `Parit di retribuzione tra uomini e donne a parit di lavoro.`, `Il diritto ad una retribuzione proporzionata alla quantit e qualit del lavoro.` ] , 
      "4928" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `L'assistenza privata non  libera.`, `La repubblica tutela il lavoro italiano all'estero.`, `La durata massima della giornata lavorativa  stabilita dalla legge.` ] , 
      "4929" : [ `Quale delle seguenti affermazioni non  conforme al quadro dei principi costituzionali contenuti nel titolo III della prima parte della Costituzione?`, `Comprimibilit dei diritti per gli inabili ed i minorati.`, `Parit di retribuzione tra uomini e donne a parit di lavoro.`, `Determinazione della durata massima della giornata lavorativa stabilita per legge.` ] , 
      "4930" : [ `Quale delle seguenti affermazioni non  conforme ai principi costituzionali riguardanti la contrattazione collettiva contenuti nel titolo III della prima parte della Costituzione?`, `I sindacati non possono stipulare contratti collettivi di lavoro.`, `Il diritto di sciopero si esercita nell'ambito delle leggi che lo regolano.`, `L'organizzazione sindacale  libera.` ] , 
      "4931" : [ `Quale delle seguenti affermazioni non  conforme ai principi costituzionali riguardanti la contrattazione collettiva contenuti nel titolo III della prima parte della Costituzione?`, `L'organizzazione sindacale non  libera.`, `Il diritto di sciopero si esercita nell'ambito delle leggi che lo regolano.`, `Ai sindacati non pu essere imposto altro obbligo se non la loro registrazione presso uffici locali o centrali.` ] , 
      "4932" : [ `Dispone la Costituzione che sono elettori tutti i cittadini, uomini e donne, che hanno raggiunto la maggiore et. Il voto :`, `Personale ed eguale, libero e segreto.`, `Collettivo ed eguale, libero e segreto.`, `Personale ed eguale, libero e pubblico.` ] , 
      "4933" : [ `Secondo la Costituzione, il voto dei cittadini :`, `Personale.`, `Plurimo.`, `Palese.` ] , 
